import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

declare const Power0;
declare const Expo;
declare const async;

export class BuildingSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new BuildingSlideController(canvas, config);
  }
}

export class BuildingSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;
    const delay = 100;

    super._handleLoad([
      /*
       * Row 0
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 0
          (callback) => _this._customRectangleClearAnimation({
            start: { x: 0, y: 0, width: 0, height: height },
            end: { x: 0, y: 0, width: width * 5 - 120, height: height },
            duration: 900, delay: 0, ease: Expo.easeIn
          }, callback),

          // Column 4
          // Color: orange
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: 5 * width - 120, y: 0 },
              end: { width: 10, height, x: 5 * width - 120, y: 0 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: '#E96621', duration: 20 },
            callback),
          // Column 4
          // Color: red #C82229
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: 5 * width - 110, y: 0 },
              end: { width: 15, height, x: 5 * width - 110, y: 0 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: '#C82229', duration: 40 },
            callback),
          // Column 4
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: 5 * width - 95, y: 0 },
              end: { width: 95, height, x: 5 * width - 95, y: 0 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: '#000', duration: 60 },
            callback),
          // Column 5
          // Color: black, opacity 0.5
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: 5 * width, y: 0 },
              end: { width: 45, height, x: 5 * width, y: 0 }
            },
            _this._sizes,
            { ease: Expo.easeOut, fill: '#000', opacity: 0.5, duration: 50 },
            callback)
          ], callback);
      },


      /*
       * Row 1
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 1
          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height, width: 0, height: height},
            end: {x: 0, y: height, width: width * 3, height: height},
            duration: 1100, delay: delay, ease: Expo.easeIn
          }, callback),
          // Column 3
          // Color: red #C82229
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 3 * width, y: height},
              end: {width: 45, height, x: 3 * width, y: height}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#C82229', duration: 100},
            callback),
          // Column 3
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 3 * width + 45, y: height},
              end: {width: 15, height, x: 3 * width + 45, y: height}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: '#000', duration: 100},
            callback)
          // END ROW 1
        ], callback);
      },


      /*
       * Row 2
       */
      function (callback) {
        let { width, height } = this;

        async.series([
          // ROW 2
          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height * 2, width: 0, height: height},
            end: {x: 0, y: height * 2, width: width * 4 - 57, height: height},
            duration: 1100, delay: delay * 2, ease: Expo.easeIn
          }, callback),
          // Column 2
          // Color: pink
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 4 * width - 57, y: height * 2},
              end: {width: 9, height, x: 4 * width - 57, y: height * 2}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#FFD2D4', duration: 50},
            callback),
          // Column 3
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {x: width * 4 - 48, y: height * 2, width: 0, height: height},
            end: {x: width * 4 - 48, y: height * 2, width: 30, height: height},
            duration: 100, delay: 0, ease: Power0.easeNone
          }, callback),
          // Column 4
          // Color: red #751017
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 4 * width - 18, y: height * 2},
              end: {width: 18, height, x: 4 * width - 18, y: height * 2}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#751017', duration: 100},
            callback),
          // Column 4
          // Color: black, opacity 0.5
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 4 * width, y: height * 2},
              end: {width: 62, height, x: 4 * width, y: height * 2}
            },
            _this._sizes,
            {ease: Expo.easeOut, fill: '#000', opacity: 0.5, duration: 150},
            callback)

          // END ROW 2
        ], callback);
      },


      /*
       * Row 3
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 3

          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height * 3, width: 0, height: height},
            end: {x: 0, y: height * 3, width: width * 4 - 18, height: height},
            duration: 1100, delay: delay * 3, ease: Expo.easeIn
          }, callback),

          // Column 4
          // Color: red #751017
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 4 * width - 18, y: height * 3},
              end: {width: 18, height, x: 4 * width - 18, y: height * 3}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#751017', duration: 100},
            callback),
          // Column 8
          // Color: pink
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 8 * width - 130, y: height * 3},
              end: {width: 8, height, x: 8 * width - 130, y: height * 3}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#FFD2D4', duration: 50},
            callback),
          // Column 8
          // Color: black, opacity: 0.5
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 8 * width - 122, y: height * 3},
              end: {width: 92, height, x: 8 * width - 122, y: height * 3}
            },
            _this._sizes,
            {ease: Expo.easeOut, fill: '#000', opacity: 0.5, duration: 250, offset: {x: 0, y: 4}},
            callback)

          // END ROW 3
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 4

          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height * 4, width: 0, height: height},
            end: {x: 0, y: height * 4, width: width * 3 - 46, height: height},
            duration: 1100, delay: delay * 4, ease: Expo.easeIn
          }, callback),

          // Column 3
          // Color: red #C82229
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 3 - 46, y: height * 4},
              end: {width: 8, height, x: width * 3 - 46, y: height * 4}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#C82229', duration: 50},
            callback),
          // Column 3
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {x: width * 3 - 38, y: height * 4, width: 0, height: height},
            end: {x: width * 3 - 38, y: height * 4, width: 38, height: height},
            duration: 50, delay: 0, ease: Power0.easeNone
          }, callback),
          // Column 4
          // Color: pink
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 3, y: height * 4},
              end: {width: 15, height, x: width * 3, y: height * 4}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: '#E96621', duration: 50},
            callback),

          // END ROW 4
        ], callback);
      },

      /*
       * Row 5
       */
      function(callback) {
        let { width, height } = this;

        _this._customRectangleClearAnimation({
            start: { x: 0, y: height * 5, width: 0, height: height },
            end: { x: 0, y: height * 5, width: width * 4, height: height },
            duration: 1100, delay: delay * 5, ease: Expo.easeIn
          }, callback);
      }
    ], config);
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    super.hide();

    const _this = this;
    const animationDuration = 1000;

    super._handleUnload([
      /*
       * Rows 0
       */
      function(callback) {
        let { fullWidth, width, height, thirdStep } = this;

        async.parallel([
          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 3 * width + width / 4, y: 0 },
            end: { width: fullWidth - 3 * width - width / 4, height: height, x: 3 * width + width / 4, y: 0 },
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 1
       */
      function(callback) {
        let { fullWidth, width, height, firstStep } = this;

        async.parallel([
          // Column 4
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width + width / 16, y: height},
            end: {width: fullWidth - width * 2 - width / 16, height: height, x: 2 * width + width / 16, y: height},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 2
       */
      function(callback) {
        let { fullWidth, width, height, secondStep } = this;

        async.parallel([
          // Column 4
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 3 * width, y: height * 2},
            end: {width: fullWidth - width * 3, height: height, x: 3 * width, y: height * 2},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 3
       */
      function(callback) {
        let {fullWidth, width, height, firstStep} = this;

        async.parallel([
          // Column 2
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width, y: height * 3},
            end: {width: fullWidth - width * 2, height: height, x: 2 * width, y: height * 3},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { fullWidth, width, height, firstStep } = this;

        async.parallel([
          // Column 2
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width, y: height * 4},
            end: {width: fullWidth - width * 2, height: height, x: 2 * width, y: height * 4},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { fullWidth, width, height, thirdStep } = this;

        async.series([
          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: width * 3, y: height * 5},
            end: {width: fullWidth - width * 3, height: height, x: width * 3, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      }
    ], config);
  }
}



