import {Component, ElementRef, EventEmitter, HostListener, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {AppService} from '../../../services/app.service/app.service';

declare const $: any;

@Component({
  selector: 'sl-options',
  templateUrl: './sl-options.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./sl-options.component.styl']
})
export class SlOptionsComponent implements OnInit {
  local: any;
  public isTextVisible = true;
  public config: any;
  public images: any;
  @Output() getSlickComponent: EventEmitter<any> = new EventEmitter();

  constructor(
    public _el: ElementRef,
    public _appService: AppService
  ) {
    this.local = {};
    this.config = {
      arrows: true,
      dots: false,
      // autoplay: true,
      speed: 600,
      infinite: true,
      slidesToShow: 1,
      draggable: false,
      prevArrow: '<button class="slick-prev"><svg version="1.1" class="svg-arrow-circle" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" \
          viewBox="0 0 21.7 21.7" style="enable-background:new 0 0 21.7 21.7;" xml:space="preserve"> \
          <path d="M10.9,0C4.9,0,0,4.9,0,10.9c0,6,4.9,10.9,10.9,10.9c6,0,10.9-4.9,10.9-10.9C21.7,4.9,16.8,0,10.9,0z M10.9,20.7 \
          c-5.4,0-9.9-4.4-9.9-9.9C1,5.4,5.4,1,10.9,1c5.4,0,9.9,4.4,9.9,9.9C20.7,16.3,16.3,20.7,10.9,20.7z M9.9,7.6l3.5,3.5l-3.5,3.5 \
          L9.4,14l2.9-2.9L9.4,8.1L9.9,7.6z"/></svg></button>',
      nextArrow: '<button class="slick-next"><svg version="1.1" class="svg-arrow-circle" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" \
          viewBox="0 0 21.7 21.7" style="enable-background:new 0 0 21.7 21.7;" xml:space="preserve"> \
          <path d="M10.9,0C4.9,0,0,4.9,0,10.9c0,6,4.9,10.9,10.9,10.9c6,0,10.9-4.9,10.9-10.9C21.7,4.9,16.8,0,10.9,0z M10.9,20.7 \
          c-5.4,0-9.9-4.4-9.9-9.9C1,5.4,5.4,1,10.9,1c5.4,0,9.9,4.4,9.9,9.9C20.7,16.3,16.3,20.7,10.9,20.7z M9.9,7.6l3.5,3.5l-3.5,3.5 \
          L9.4,14l2.9-2.9L9.4,8.1L9.9,7.6z"/></svg></button>',
      responsive: [
        {
          breakpoint: 740,
          settings: {
            dots: true
          }
        }
      ]
    };
  }

  ngOnInit() {
    this.images = [...this._appService.getImage('optionsImages')];
  }

  saveSlickElement(data) {
    this.getSlickComponent.emit(data);
  }

  clicked() {
    console.log(3);
    $(this._el.nativeElement).find('.sl-options').removeClass('_active');
  }
  @HostListener('document:click') onClick() {
    $(this._el.nativeElement).find('.sl-options').removeClass('_active');
  }

  textHide() {
    this.isTextVisible = !this.isTextVisible;
  }

}



