import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

declare const Power0;
declare const Expo;
declare const async;

export class ContactSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new ContactSlideController(canvas, config);
  }
}

export class ContactSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;

    super._handleLoad([

      /*
       * Row 0
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight, startImageX, startImageY } = this;

        async.parallel([

          // Column 4-5
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 4 + 55, y: 0, width: 0, height},
              image: {
                x: imageWidth * 4 + startImageX + 70,
                y: startImageY,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 4 + 55, y: 0, width: width * 2 - 70, height},
              image: {
                x: imageWidth * 4 + startImageX + 70,
                y: startImageY,
                width: imageWidth * 2 - 80,
                height: imageHeight
              },
            },
            duration: 1300,
            delay: firstStep,
            ease: Expo.easeInOut
          }),

          // Column 6
          (callback) => async.series([
            // Color: red #751017
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 6 * width - 15, y: 0},
                end: {width: 5, height: height, x: 6 * width - 15, y: 0}
              },
              _this._sizes,
              {
                ease: Expo.easeIn,
                isNotRedrawImage: true,
                fill: '#751017',
                duration: 150,
                delay: firstStep + 300
              },
              callback),

            // Color: pink
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 6 * width - 10, y: 0},
                end: {width: 10, height: height, x: 6 * width - 10, y: 0}
              },
              _this._sizes,
              {
                ease: Power0.easeNone,
                isNotRedrawImage: true,
                fill: '#FFD2D4',
                duration: 150
              },
              callback),

            // Color: black, opacity: 0.4
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 6 * width, y: 0},
                end: {width: 125, height: height, x: 6 * width, y: 0}
              },
              _this._sizes,
              {
                ease: Expo.easeOut,
                fill: '#000',
                opacity: 0.4,
                duration: 300
              },
              callback)
          ], callback),

          // Column 7
          // Color: red #C82229
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: 7 * width + 65, y: 0},
            end: {width: width - 65, height: height, x: 7 * width + 65, y: 0}
          },
          _this._sizes,
          {
            ease: Expo.easeInOut,
            isNotRedrawImage: true,
            fill: '#C82229',
            duration: 400,
            delay: firstStep + 300
          },
          callback),
        ], callback);
      },


      /*
       * Row 1
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.parallel([
          // ROW 1

          // Column 4
          (callback) => async.series([
            // Color: orange
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 4 * width - 55, y: height},
                end: {width: 50, height: height, x: 4 * width - 55, y: height}
              },
              _this._sizes,
              {
                ease: Expo.easeIn, isNotRedrawImage: true,
                fill: '#E96621', opacity: 1, duration: 200, delay: firstStep + 200
              },
              callback),

            // Color: red #751017
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 4 * width - 5, y: height},
                end: {width: 5, height: height, x: 4 * width - 5, y: height}
              },
              _this._sizes,
              {
                ease: Expo.easeOut, isNotRedrawImage: true,
                fill: '#751017', opacity: 1, duration: 150
              },
              callback),
          ], callback),

          // Column 7
          (callback) => async.series([
            // Color: red #C82229
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 7 * width - 5, y: height},
                end: {width: 5, height: height, x: 7 * width - 5, y: height}
              },
              _this._sizes,
              {
                ease: Expo.easeIn, isNotRedrawImage: true,
                fill: '#C82229', opacity: 1, duration: 150, delay: firstStep + 200
              },
              callback),

            // Color: black
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 7 * width, y: height},
                end: {width: 30, height: height, x: 7* width, y: height}
              },
              _this._sizes,
              {
                ease: Expo.easeOut, isNotRedrawImage: true,
                fill: '#000', opacity: 1, duration: 200
              },
              callback),
          ], callback)

          // END ROW 1
        ], callback)
      },


      /*
       * Row 2
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.parallel([
          // ROW 2

          // Column 7
          // Color: orange
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 7 * width - 45, y: height * 2},
              end: {width: 5, height: height, x: 7 * width - 45, y: height * 2}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: '#E96621', duration: 300, delay: firstStep},
            callback)

          // END ROW 2
        ], callback)
      },


      /*
       * Row 3
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 3

          // Column: 3-4
          // Color: black, opacity: 0.4
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 4 * width - 70, y: height * 3},
              end: {width: width + 10, height: height, x: 4 * width - 70, y: height * 3}
            },
            _this._sizes,
            {
              ease: Expo.easeIn,
              fill: '#000',
              duration: 300,
              opacity: 0.4,
              delay: firstStep
            },
            callback),

          // Column: 5
          // Color: pink
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 5 * width - 60, y: height * 3},
              end: {width: 10, height: height, x: 5 * width - 60, y: height * 3}
            },
            _this._sizes,
            {
              isNotRedrawImage: true,
              fill: '#FFD2D4',
              ease: Power0.easeNone,
              duration: 100
            },
            callback),

          // Column: 5
          // Color: red #C82229
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 5 * width - 50, y: height * 3},
              end: {width: 20, height: height, x: 5 * width - 50, y: height * 3}
            },
            _this._sizes,
            {
              isNotRedrawImage: true,
              fill: '#C82229',
              ease: Expo.easeOut,
              duration: 200
            },
            callback),

          // END ROW 3
        ], callback)
      },


      /*
       * Row 4
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight, startImageX, startImageY } = this;

        async.parallel([
          // ROW 4

          // Column 3-7
          (callback) => async.series([
            // Image animate
            _this._rectangleOffsetAnimate({
              start: {
                canvas: {x: width * 3 + 110, y: height * 4, width: 0, height},
                image: {
                  x: imageWidth * 3 + 110 + startImageX - 15,
                  y: imageHeight * 4 + startImageY,
                  width: 0,
                  height: imageHeight
                },
              },
              end: {
                canvas: {x: width * 3 + 110, y: height * 4, width: width * 3, height},
                image: {
                  x: imageWidth * 3 + 110 + startImageX - 15,
                  y: imageHeight * 4 + startImageY,
                  width: imageWidth * 3,
                  height: imageHeight
                },
              },
              duration: 780,
              ease: Expo.easeIn,
              delay: firstStep
            }),

            (callback) => async.parallel([
              // Image animate with black fill
              _this._rectangleOffsetAnimate({
                start: {
                  canvas: {x: width * 6, y: height * 4, width: 0, height},
                  image: {
                    x: imageWidth * 6 + startImageX - 15,
                    y: imageHeight * 4 + startImageY,
                    width: 0,
                    height: imageHeight
                  },
                },
                end: {
                  canvas: {x: width * 6, y: height * 4, width: width * 2, height},
                  image: {
                    x: imageWidth * 6 + startImageX - 15,
                    y: imageHeight * 4 + startImageY,
                    width: imageWidth * 2,
                    height: imageHeight
                  },
                },
                duration: 520,
                ease: Expo.easeOut,
                onEndRedraw: (context, current) => {
                  CanvasRect.draw(context, {
                    x: current.canvasX, y: current.canvasY, width: current.canvasWidth, height: current.canvasHeight,
                    opacity: 0.4, fill: '#000'
                  });
                }
              }),

              // Column 6
              // Color: red #751017
              (callback) => _this._customRectangleAnimation({
                  start: {width: 0, height: height, x: 6 * width - 40, y: height * 4},
                  end: {width: 10, height: height, x: 6 * width - 40, y: height * 4}
                },
                _this._sizes,
                {ease: Expo.easeIn, isNotRedrawImage: true, fill: '#751017', duration: 300},
                callback),

              // Column 6
              // Color: red #C82229
              (callback) => _this._customRectangleAnimation({
                  start: {width: 0, height: height, x: 6 * width - 30, y: height * 4},
                  end: {width: 30, height: height, x: 6 * width - 30, y: height * 4}
                },
                _this._sizes,
                {ease: Expo.easeOut, isNotRedrawImage: true, fill: '#C82229', duration: 300, delay: 150},
                callback)
            ], callback)
          ], callback),

          // Column 3
          (callback) => async.series([
            // Color: red #751017
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 3 * width - 50, y: height * 4},
                end: {width: 5, height: height, x: 3 * width - 50, y: height * 4}
              },
              _this._sizes,
              {ease: Expo.easeIn, isNotRedrawImage: true, fill: '#751017', duration: 250, delay: firstStep},
              callback),

            // Color: red #C82229
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 3 * width - 45, y: height * 4},
                end: {width: 125, height: height, x: 3 * width - 45, y: height * 4}
              },
              _this._sizes,
              {ease: Power0.easeNone, isNotRedrawImage: true, fill: '#C82229', duration: 300},
              callback),

            // Color: black
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 3 * width + 80, y: height * 4},
                end: {width: 30, height: height, x: 3 * width + 80, y: height * 4}
              },
              _this._sizes,
              {ease: Expo.easeOut, isNotRedrawImage: true, fill: '#000', duration: 200},
              callback),

          ], callback)

          // END ROW 4
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 5

          // Column 3-4
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 4 * width - 115, y: height * 5},
              end: {width: 45, height: height, x: 4 * width - 115, y: height * 5}
            },
            _this._sizes,
            {
              isNotRedrawImage: true,ease: Expo.easeIn, fill: '#000', duration: 300, delay: firstStep,
            },
            callback),

          // Column 3-4
          // Color: red #751017
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 4 * width - 70, y: height * 5},
              end: {width: 60, height: height, x: 4 * width - 70, y: height * 5}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: '#751017', duration: 350},
            callback),

          // Column 3-4
          // Color: pink
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 4 * width - 10, y: height * 5},
              end: {width: 10, height: height, x: 4 * width - 10, y: height * 5}
            },
            _this._sizes,
            {ease: Expo.easeInOut, isNotRedrawImage: true, fill: '#FFD2D4', duration: 100},
            callback),

          // END ROW 5
        ], callback);
      }
    ], config);
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    super.hide();

    const _this = this;
    const animationDuration = 1000;

    super._handleUnload([
      /*
       * Rows 0
       */
      function(callback) {
        let { fullWidth, width, height, thirdStep } = this;

        async.parallel([
          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 3 * width + width / 4, y: 0 },
            end: { width: fullWidth - 3 * width - width / 4, height: height, x: 3 * width + width / 4, y: 0 },
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 1
       */
      function(callback) {
        let { fullWidth, width, height, firstStep } = this;

        async.parallel([
          // Column 4
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width + 10, y: height},
            end: {width: fullWidth - width * 2 - 10, height: height, x: 2 * width + 10, y: height},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 2
       */
      function(callback) {
        let { fullWidth, width, height, secondStep } = this;

        async.parallel([
          // Column 4
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 3 * width, y: height * 2},
            end: {width: fullWidth - width * 3, height: height, x: 3 * width, y: height * 2},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 3
       */
      function(callback) {
        let {fullWidth, width, height, firstStep} = this;

        async.parallel([
          // Column 2
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width, y: height * 3},
            end: {width: fullWidth - width * 2, height: height, x: 2 * width, y: height * 3},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { fullWidth, width, height, firstStep } = this;

        async.parallel([
          // Column 2
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width, y: height * 4},
            end: {width: fullWidth - width * 2, height: height, x: 2 * width, y: height * 4},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { fullWidth, width, height, thirdStep } = this;

        async.series([
          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: width * 3, y: height * 5},
            end: {width: fullWidth - width * 3, height: height, x: width * 3, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      }
    ], config);
  }
}



