import {Injectable, NgZone} from '@angular/core';
import {Slide} from "./slide";
import {ISlideConfig} from "./ISlideConfig";
import {Router, NavigationEnd} from "@angular/router";
import 'rxjs/add/operator/filter';

import {HomeSlideControllerFactory} from "../../shared/slider/desktop/homeSlideController";
import {BuildingSlideControllerFactory} from "../../shared/slider/desktop/buildingSlideController";
import {NeighborhoodSlideControllerFactory} from "../../shared/slider/desktop/neighborhoodSlideController";
import {ContactSlideControllerFactory} from "../../shared/slider/desktop/contactSlideController";

import {HomeMobileSlideControllerFactory} from "../../shared/slider/mobile/homeMobileSlideController";
import {BuildingMobileSlideControllerFactory} from "../../shared/slider/mobile/buildingMobileSlideController";
import {NeighborhoodMobileSlideControllerFactory} from "../../shared/slider/mobile/neighborhoodMobileSlideController";
import {ContactMobileSlideControllerFactory} from "../../shared/slider/mobile/contactMobileSlideController";

declare const $;

@Injectable()
export class GreetingService {
  public isButtonsDisabled: boolean = true;
  public isCenterTitle: boolean;
  public isTitleToRight: boolean;
  public isOpenMenu: boolean = false;
  public isArrowVisible = false;
  public isCanChangeState = false;
  public isFooterVisible: boolean = false;

  private _slideConfigs: ISlideConfig[] = [
    {
      prevState: { name: 'contact', text: 'Contact' },
      nextState: { name: 'building', text: 'Building' },
      state: { name: '', text: 'Home' },
      controllerFactory: {
        desktop: new HomeSlideControllerFactory(),
        mobile: new HomeMobileSlideControllerFactory()
      },
      title: [{ text: 'west', name: '_half' }, { text: 'half', name: '_half-ruble' }, { text: 'a shift', name: '_quarter' }, { text: 'in urban', name: '_three-quarter' }, { text: 'living', name: '_zero' }],
      subtitle: [{ text: 'APARTMENTS LEASING SUMMER 2019', name: '_apartments' }]
    },
    {
      prevState: { name: '', text: 'Home' },
      nextState: { name: 'neighborhood', text: 'Neighborhood' },
      state: { name: 'building', text: 'Building' },
      title: [{ text: 'a shift', name: '_zero' }, { text: 'in', name: '_half-ruble' }, { text: 'balance.', name: '_half' }],
      comment: [{ text: 'building', name: '_shift-end' }],
      controllerFactory: {
        desktop: new BuildingSlideControllerFactory(),
        mobile: new BuildingMobileSlideControllerFactory()
      }
    },
    {
      prevState: { name: 'building', text: 'Building' },
      nextState: { name: 'contact', text: 'Contact' },
      state: { name: 'neighborhood', text: 'Neighborhood' },
      title: [{ text: 'a shift', name: '_zero' }, { text: 'in', name: '_half-ruble' }, { text: 'culture.', name: '_half' }],
      comment: [{ text: 'neighborhood', name: '_shift-end' }],
      controllerFactory: {
        desktop: new NeighborhoodSlideControllerFactory(),
        mobile: new NeighborhoodMobileSlideControllerFactory()
      }
    },
    {
      prevState: { name: 'neighborhood', text: 'Neighborhood' },
      nextState: { name: '', text: 'Home' },
      state: { name: 'contact', text: 'Contact' },
      title: [{ text: 'a shift', name: '_zero' }, { text: 'in', name: '_half-ruble' }, { text: 'reality.', name: '_half' }],
      comment: [{ text: 'contact', name: '_shift-end' }],
      controllerFactory: {
        desktop: new ContactSlideControllerFactory(),
        mobile: new ContactMobileSlideControllerFactory()
      }
    }
  ];

  private _slides: Slide[] = [];

  private _current: Slide;

  constructor(
    private _ngZone: NgZone,
    private _router: Router
  ) {
  }

  public initSlider(canvas, config, type) {
    const self = this;

    $(config.components.images).find('.item').each(function() {
      const $that = $(this);
      const imgSrc = $that.find('img').attr('src');
      const index = $that.index();

      self._slideConfigs[index].image = imgSrc;
    });

    this._slideConfigs.forEach((slideConfig) => {
      this._slides.push(new Slide(canvas, Object.assign({}, slideConfig, config), type));
    });

    let firstStateListener = this._router.events
      .filter((route) => route instanceof NavigationEnd)
      .subscribe((route: any) => {
        let stateName = route.url.slice(1);

        this._current = this.findSlide(stateName);
        this._showState();

        firstStateListener.unsubscribe();
      });
  }

  public get current(): Slide {
    return this._current;
  }

  public get next() {
    let stateName = this.current.nextState.name;

    return this.findSlide(stateName);
  }

  public get prev() {
    let stateName = this.current.prevState.name;

    return this.findSlide(stateName);
  }

  private _showState(callback?: Function) {
    let perform = performance.now();
    this._current.show({
      onEnd: () => {
        console.debug(`GreetingService: _showState: ${performance.now() - perform}`);

        callback && callback();
      }
    });
  }

  private _hideState(state, hideCallback: Function, showCallback: Function) {
    let perform = performance.now();
    this.current.hide({
      onEnd: () => {
        console.debug(`GreetingService: _hideState: ${performance.now() - perform}`);

        this._current = state;
        this._router.navigate([state.state.name]);
        hideCallback();
        this._showState(showCallback);
      }
    });
  }

  public toNext(hideCallback: Function, showCallback: Function) {
    let stateName = this.current.nextState.name;

    this.changeTo(stateName, hideCallback, showCallback);
  }

  public toPrev(hideCallback: Function, showCallback: Function) {
    let stateName = this.current.prevState.name;

    this.changeTo(stateName, hideCallback, showCallback);
  }

  public changeTo(stateName: string, hideCallback: Function, showCallback: Function) {
    let state = this.findSlide(stateName);

    this._hideState(state, hideCallback, showCallback);
  }

  public findSlide(state: String) {
    return this._slides.find((slide) =>
      slide.state.name === state);
  }

  public getImages(): String[] {
    return this._slideConfigs.map((config) => config.image);
  }

  /*
   *
   */

  public remoteTitleToCenter() {
    this.isCenterTitle = false;
  }
  public titleToCenter() {
    this.isCenterTitle = true;
  }

  public remoteTitleToRight() {
    this.isTitleToRight = false;
  }
  public titleToRight() {
    this.isTitleToRight = true;
  }

  public hideArrow() {
    this.isArrowVisible = false;
  }
  public showArrow() {
    setTimeout(() => {
      this.isArrowVisible = this.current.state && this.current.state.name !== '';
    }, 2000);
  }

  public swapTitleToLeft() {
    this._ngZone.run(() => {
      setTimeout(() => {
        this.isCanChangeState = true;
        this.isFooterVisible = true;
      }, 2000);

      this.remoteTitleToRight();
      this.titleToCenter();
      this.showArrow();
      this.isButtonsDisabled = false;
    });
  }

  private _prepareChangeState() {
    this.isCanChangeState = false;

    this.closeMenu();
    this.isButtonsDisabled = true;
    this.hideArrow();
    this.titleToRight();
    this.remoteTitleToCenter();
    this.isFooterVisible = false;
  }

  public toState(name) {
    this._prepareChangeState();

    return this[name](
      () => this.swapTitleToLeft(),
      () => {});
  }

  public goToState(stateName: string) {
    if (this.current.state.name === stateName) { return; }

    this._prepareChangeState();

    return this.changeTo(
      stateName,
      () => this.swapTitleToLeft(),
      () => {});
  }

  closeMenu() {
    this.isOpenMenu = false;
  }
  openMenu() {
    this.isOpenMenu = true;
  }

  swipe(direction: string) {
    if (!this.isCanChangeState) { return; }

    direction === 'swipeleft' ? this.toState('toNext') : this.toState('toPrev');
  }
}



