import {Component, HostListener, OnInit, ViewEncapsulation} from '@angular/core';

@Component({
  selector: 's-options',
  templateUrl: './s-options.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./s-options.component.styl']
})
export class SOptionsComponent implements OnInit {
  slick: any;
  constructor() {
  }

  ngOnInit() {
  }

  saveSlickComponent(slick) {
    this.slick = slick;
  }
  clicked(event) {
    event.stopPropagation();
  }


}



