import {
  Component, OnInit, ViewEncapsulation, Input, Output, EventEmitter, ElementRef,
  HostListener
} from '@angular/core';
import {GreetingService} from '../../../services/greeting/greeting.service';

@Component({
  selector: 'b-home-popup',
  templateUrl: './b-home-popup.component.html',
  styleUrls: ['./b-home-popup.component.styl'],
  encapsulation: ViewEncapsulation.None
})
export class BHomePopupComponent implements OnInit {
  @Input() isPopupVisible;
  @Output() onClosePopup: EventEmitter<any> = new EventEmitter();

  public get isButtonDisabled() {
    return this._greetingService.isButtonsDisabled;
  }

  constructor(
    private _greetingService: GreetingService,
    public _el: ElementRef
  ) { }

  @HostListener('document:click', ['$event']) removePopup(event) {
    if (!this._el.nativeElement.contains(event.target)) {
      this.hidePopup();
    }
  }

  ngOnInit() {
  }
  hidePopup() {
    this.onClosePopup.emit();
  }

  public get current(): any {
    return this._greetingService.current || {};
  }

  public get title() {
    return this.current.title || [];
  }
  public get subtitle() {
    return this.current.subtitle || [];
  }
  public get comment() {
    return this.current.comment || [];
  }

  public get prev() {
    return this.current.prevState;
  }
  public get next() {
    return this.current.nextState;
  }

  toPrev() {
    this._greetingService.toState('toPrev');
  }
  toNext() {
    this._greetingService.toState('toNext');
  }

  public onState(state) {
    this.onClosePopup.emit();

    setTimeout(() => {
      state === 'next' ? this.toNext() : this.toPrev();
    }, 700);
  }
}



