import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

import { colors } from '../slider-colors';

declare const Power0;
declare const Expo;
declare const async;

export class NeighborhoodMobileSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new NeighborhoodMobileSlideController(canvas, config);
  }
}

export class NeighborhoodMobileSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;

    super._handleLoad([
      /*
       * Row 0
       */
      function (callback) {
        let { secondStep, width, height } = this;

        async.parallel([
          // Column 0-1
          (callback) => async.series([
            // Column 0-1
            (callback) => _this._customRectangleClearAnimation({
              start: { x: 0, y: 0, width: 0, height: height },
              end: { x: 0, y: 0, width: width * 1.76, height: height },
              duration: 1200, delay: 150, ease: Expo.easeIn
            }, callback),

            // Column 1
            // Color: black
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 1.76, y: 0},
                end: {width: width * 0.18, height: height, x: width * 1.76, y: 0}
              },
              _this._sizes,
              {
                ease: Power0.None, isNotRedrawImage: true, fill: colors.black, duration: 100
              },
              callback),

            // Column 1
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 1.94, y: 0},
                end: {width: width * 0.06, height: height, x: width * 1.94, y: 0}
              },
              _this._sizes,
              {
                ease: Expo.easeOut, isNotRedrawImage: true, fill: colors.red75, duration: 100
              },
              callback),
          ], callback),

          // Column 4
          (callback) => async.series([

            // Column 4
            // Color: redC8
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 4 * width, y: 0},
                end: {width: width * 0.08, height: height, x: 4 * width, y: 0}
              },
              _this._sizes,
              {
                ease: Expo.easeIn, isNotRedrawImage: true,
                fill: colors.redC8, opacity: 1, duration: 100,
                delay: secondStep
              },
              callback),

            // Column 4
            // Color: orangeE9
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 4.08, y: 0},
                end: {width: width * 0.1, height: height, x: width * 4.08, y: 0}
              },
              _this._sizes,
              {
                ease: Expo.easeOut, isNotRedrawImage: true,
                fill: colors.orangeE9, opacity: 1, duration: 100
              },
              callback),

            // Column 4-5
            (callback) => _this._customRectangleClearAnimation({
              start: { x: width * 4.18, y: 0, width: 0, height: height },
              end: { x: width * 4.18, y: 0, width: width * 1.82, height: height },
              duration: 1000, ease: Expo.easeOut
            }, callback)

          ], callback)
        ], callback);
      },

      /*
       * Row 1
       */
      function (callback) {
        let { width, height, firstStep } = this;

        async.series([
          // Column 4
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 4 * width, y: height},
              end: {width: width * 0.08, height: height, x: 4 * width, y: height}
            },
            _this._sizes,
            {
              ease: Expo.easeIn, isNotRedrawImage: true,
              fill: colors.redC8, opacity: 1, duration: 100,
              delay: firstStep
            },
            callback),

          // Column 4-5
          (callback) => _this._customRectangleClearAnimation({
            start: { x: width * 4.08, y: height, width: 0, height: height },
            end: { x: width * 4.08, y: height, width: width * 1.92, height: height },
            duration: 1000, ease: Expo.easeOut
          }, callback)

        ], callback);
      },

      /*
       * Row 2
       */
      function (callback) {
        let { width, height, firstStep, secondStep } = this;

        console.log(firstStep, secondStep);
        async.series([
          // Column 3
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 1.95, y: height * 2},
              end: {width: width * 0.05, height: height, x: width * 1.95, y: height * 2}
            },
            _this._sizes,
            {
              ease: Expo.easeIn, isNotRedrawImage: true,
              fill: colors.pinkFF, opacity: 1, duration: (secondStep - firstStep) * 0.25,
              delay: firstStep
            },
            callback),
          // Column 3
          // Color: black, opacity: 0.4
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 2, y: height * 2},
              end: {width: width * 2, height: height, x: width * 2, y: height * 2}
            },
            _this._sizes,
            {
              ease: Expo.easeIn, opacity: 0.4,
              fill: colors.black, duration: (secondStep - firstStep) * 0.75
            },
            callback),

          // Column 4
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 4 * width, y: height * 2},
              end: {width: width * 0.08, height: height, x: 4 * width, y: height * 2}
            },
            _this._sizes,
            {
              ease: Expo.easeIn, isNotRedrawImage: true,
              fill: colors.redC8, opacity: 1, duration: 100
            },
            callback),

          // Column 4-5
          (callback) => _this._customRectangleClearAnimation({
            start: { x: width * 4.08, y: height, width: 0, height: height * 2 },
            end: { x: width * 4.08, y: height, width: width * 1.92, height: height * 2 },
            duration: 1000, ease: Expo.easeOut
          }, callback)

        ], callback);
      },

      /*
       * Row 3
       */
      function (callback) {
        let { width, height, secondStep } = this;

        async.parallel([
          // Column 5
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height: height, x: width * 5.85, y: height * 3 },
              end: { width: width * 0.15, height: height, x: width * 5.85, y: height * 3 }
            },
            _this._sizes,
            {
              ease: Expo.easeOut, fill: colors.orangeE9, duration: 100, isNotRedrawImage: true, delay: secondStep
            },
            callback),

        ], callback);
      },

      /*
       * Row 4
       */
      function (callback) {
        let { width, height, firstStep, secondStep } = this;

        async.parallel([

          // Column 0-1
          (callback) => async.series([
            // Column 0
            // Color: redC8
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 0.24, y: height * 4 },
                end: { width: width * 0.17, height: height, x: width * 0.24, y: height * 4 }
              },
              _this._sizes,
              {
                ease: Expo.easeIn, fill: colors.redC8, duration: 200, delay: firstStep, isNotRedrawImage: true,
              },
              callback),
            // Column 0
            // Color: pinkFF
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 0.41, y: height * 4 },
                end: { width: width * 0.04, height: height, x: width * 0.41, y: height * 4 }
              },
              _this._sizes,
              {
                ease: Power0.easeNone, fill: colors.pinkFF, duration: 100, isNotRedrawImage: true,
              },
              callback),
            // Column 0
            // Color: black
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 0.45, y: height * 4 },
                end: { width: width * 0.2, height: height, x: width * 0.45, y: height * 4 }
              },
              _this._sizes,
              {
                ease: Power0.easeNone, fill: colors.black, duration: 150, isNotRedrawImage: true
              },
              callback),

            // Column 0-1
            // Color: black, opacity: 0.4
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 0.65, y: height * 4 },
                end: { width: width * 1.10, height: height, x: width * 0.65, y: height * 4 }
              },
              _this._sizes,
              {
                ease: Power0.easeNone, fill: colors.black, opacity: 0.4, duration: 350
              },
              callback),
            // Column 1
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 1.75, y: height * 4 },
                end: { width: width * 0.2, height: height, x: width * 1.75, y: height * 4 }
              },
              _this._sizes,
              {
                ease: Power0.easeNone, fill: colors.red75, duration: 150, isNotRedrawImage: true
              },
              callback),
            // Column 1
            // Color: orangeE9
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 1.95, y: height * 4 },
                end: { width: width * 0.05, height: height, x: width * 1.95, y: height * 4 }
              },
              _this._sizes,
              {
                ease: Expo.easeOut, fill: colors.orangeE9, duration: 100, isNotRedrawImage: true
              },
              callback),
          ], callback),

          // Column 5
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height: height, x: width * 5.85, y: height * 4 },
              end: { width: width * 0.15, height: height, x: width * 5.85, y: height * 4 }
            },
            _this._sizes,
            {
              ease: Expo.easeOut, fill: colors.orangeE9, duration: 100, isNotRedrawImage: true, delay: secondStep
            },
            callback),

        ], callback);
      },

      /*
       * Row 5-6
       */
      function (callback) {
        let { width, height, firstStep } = this;

        async.series([

          // Column 4
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height: height * 2, x: width * 4, y: height * 5 },
              end: { width: width * 0.05, height: height * 2, x: width * 4, y: height * 5 }
            },
            _this._sizes,
            {
              ease: Expo.easeOut, fill: colors.orangeE9, opacity: 1, duration: 70, isNotRedrawImage: true, delay: firstStep
            },
            callback),
          // Column 4
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height: height * 2, x: width * 4.05, y: height * 5 },
              end: { width: width * 0.20, height: height * 2, x: width * 4.05, y: height * 5 }
            },
            _this._sizes,
            {
              ease: Expo.easeOut, fill: colors.redC8, opacity: 1, duration: 120, isNotRedrawImage: true
            },
            callback),
          // Column 4-5
          (callback) => _this._customRectangleClearAnimation({
            start: { x: width * 4.25, y: height * 5, width: 0, height: height * 2 },
            end: { x: width * 4.25, y: height * 5, width: width * 1.75, height: height * 2},
            duration: 800, ease: Expo.easeInOut
          }, callback),


        ], callback);
      },

      /*
       * Row 8
       */
      function (callback) {
        let { width, height, firstStep, secondStep } = this;

        async.parallel([

          // Column 0-2
          (callback) => async.series([
            // Column 0
            (callback) => _this._customRectangleClearAnimation({
              start: { x: 0, y: height * 8, width: 0, height: height },
              end: { x: 0, y: height * 8, width: width * 0.55, height: height },
              duration: 500, ease: Expo.easeIn,
              delay: firstStep
            }, callback),

            // Column 0
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 0.55, y: height * 8 },
                end: { width: width * 0.70, height: height, x: width * 0.55, y: height * 8 }
              },
              _this._sizes,
              {
                ease: Power0.easeNone, fill: colors.red75, duration: 100, isNotRedrawImage: true
              },
              callback),

            // Column 1
            (callback) => _this._customRectangleClearAnimation({
              start: { x: width * 1.25, y: height * 8, width: 0, height: height },
              end: { x: width * 1.25, y: height * 8, width: width * 0.75, height: height },
              duration: 150, ease: Power0.easeNone,
              onEndRedraw: (context, current) => {
                CanvasRect.draw(context, {
                  width: current.width, height: current.height, x: current.x, y: current.y, fill: colors.black, opacity: 0.4
                });
              }
            }, callback),

            // Column 2
            // Color: black, opacity: 0.4
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 2, y: height * 8 },
                end: { width: width * 0.3, height: height, x: width * 2, y: height * 8 }
              },
              _this._sizes,
              {
                ease: Expo.easeOut, fill: colors.black, opacity: 0.4, duration: 100
              },
              callback),
            // Column 2
            // Color: pinkFF
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 2.3, y: height * 8 },
                end: { width: width * 0.05, height: height, x: width * 2.3, y: height * 8 }
              },
              _this._sizes,
              {
                ease: Expo.easeInOut, fill: colors.pinkFF, isNotRedrawImage: true, duration: 100
              },
              callback),
          ], callback),

          // Column 5
          (callback) => async.series([
            // Color: black, opacity: 0.8
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 5, y: height * 8 },
                end: { width: width * 0.35, height: height, x: width * 5, y: height * 8 }
              },
              _this._sizes,
              {
                ease: Expo.easeIn, fill: colors.black, duration: 150, opacity: 0.8, delay: secondStep
              },
              callback),
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 5.35, y: height * 8 },
                end: { width: width * 0.2, height: height, x: width * 5.35, y: height * 8 }
              },
              _this._sizes,
              {
                ease: Expo.easeOut, fill: colors.red75, duration: 150, isNotRedrawImage: true
              },
              callback),
            // Color: pinkFF
            (callback) => _this._customRectangleAnimation({
                start: { width: 0, height: height, x: width * 5.55, y: height * 8 },
                end: { width: width * 0.05, height: height, x: width * 5.55, y: height * 8 }
              },
              _this._sizes,
              {
                ease: Expo.easeInOut, fill: colors.pinkFF, duration: 150, isNotRedrawImage: true
              },
              callback),

          ], callback)

        ], callback);
      },

      /*
       * Row 9
       */
      function (callback) {
        let { firstStep, width, height } = this;

        async.parallel([
          // Column 0-1
          (callback) => _this._customRectangleClearAnimation({
            start: { x: 0, y: height * 9, width: 0, height: height },
            end: { x: 0, y: height * 9, width: width * 2, height: height },
            duration: 800, delay: firstStep, ease: Expo.easeIn
          }, callback),

        ], callback);
      }
    ], config);
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    super.hide();

    const _this = this;
    const animationDuration = 1000;

    super._handleUnload([
      /*
       * Rows 0
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 0, y: 0 },
            end: { width: fullWidth, height: height, x: 0, y: 0 },
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 1
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height},
            end: {width: fullWidth, height: height, x: 0, y: height},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 2
       */
      function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0},
            end: {width: fullWidth, height: height, x: 0},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 3
       */
      function(callback) {
        let {fullWidth, height, firstStep} = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 3},
            end: {width: fullWidth, height: height, x: 0, y: height * 3},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 4},
            end: {width: fullWidth, height: height, x: 0, y: height * 4},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 5},
            end: {width: fullWidth, height: height, x: 0, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Row 6
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 6},
            end: {width: fullWidth, height: height, x: 0, y: height * 6},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 7
       */
      function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 7},
            end: {width: fullWidth, height: height, x: 0, y: height * 7},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Row 8
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 8},
            end: {width: fullWidth, height: height, x: 0, y: height * 8},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 9
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 9},
            end: {width: fullWidth, height: height, x: 0, y: height * 9},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      }
    ], config);
  }
}



