import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

declare const Power0;
declare const Expo;
declare const async;

import { colors } from '../slider-colors';

export class ContactMobileSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new ContactMobileSlideController(canvas, config);
  }
}

export class ContactMobileSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;

    super._handleLoad([

      /*
       * Row 0
       */
       function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight, startImageX, startImageY } = this;

        async.parallel([

          // Column 1-3
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 1.68, y: 0, width: 0, height},
              image: {
                x: imageWidth * 2,
                y: startImageY,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 1.68, y: 0, width: width * 2.2, height},
              image: {
                x: imageWidth * 2,
                y: startImageY,
                width: imageWidth * 2.2,
                height: imageHeight
              },
            },
            duration: 900,
            delay: firstStep,
            ease: Expo.easeInOut
          }),

          // Column 3
          (callback) => async.series([
            // Color: red75
            (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 3.88, y: 0},
              end: {width: width / 20, height: height, x: width * 3.88, y: 0}
            },
            _this._sizes,
            {
              ease: Expo.easeIn,
              isNotRedrawImage: true,
              fill: colors.red75,
              duration: 150,
              delay: firstStep + 700
            },
            callback),

            // Column 3
            // Color: pinkFF
            (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 3.93, y: 0},
              end: {width: width * 0.07, height: height, x: width * 3.93, y: 0}
            },
            _this._sizes,
            {
              ease: Power0.easeNone,
              isNotRedrawImage: true,
              fill: colors.pinkFF,
              duration: 150
            },
            callback),

            // Column 4
            // Color: black, opacity: 0.4
            (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4, y: 0},
              end: {width: width * 0.9, height: height, x: width * 4, y: 0}
            },
            _this._sizes,
            {
              ease: Expo.easeOut,
              fill: colors.black,
              opacity: 0.4,
              duration: 300
            },
            callback)
            ], callback),

          // Column 5
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: 5.7 * width, y: 0},
            end: {width: width * 0.3, height: height, x: 5.7 * width, y: 0}
          },
          _this._sizes,
          {
            ease: Expo.easeInOut,
            isNotRedrawImage: true,
            fill: colors.redC8,
            duration: 400,
            delay: firstStep + 400
          },
          callback),
          ], callback);
      },


      /*
       * Row 1
       */
       function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 1

          // Column 0
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 0.68, y: height},
            end: {width: width * 0.64, height: height, x: width * 0.68, y: height}
          },
          _this._sizes,
          {
            ease: Expo.easeIn, isNotRedrawImage: true,
            fill: colors.orangeE9, opacity: 1, duration: 200, delay: firstStep + 200
          },
          callback),

          // Column 1
          // Color: red75
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 1.32, y: height},
            end: {width: width * 0.08, height: height, x: width * 1.32, y: height}
          },
          _this._sizes,
          {
            ease: Expo.easeOut, isNotRedrawImage: true,
            fill: colors.red75, opacity: 1, duration: 150
          },
          callback)

          // END ROW 1
          ], callback)
      },


      /*
       * Row 3
       */
       function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 3

          // Column: 0
          // Color: red75
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: 0, y: height * 3},
            end: {width: width * 0.2, height: height, x: 0, y: height * 3}
          },
          _this._sizes,
          {
            ease: Expo.easeIn,
            fill: colors.red75,
            duration: 300,
            isNotRedrawImage: true,
            delay: firstStep
          },
          callback),

          // Column: 0
          // Color: black, opacity 0.8
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 0.2, y: height * 3},
            end: {width: width * 0.1, height: height, x: width * 0.2, y: height * 3}
          },
          _this._sizes,
          {
            opacity: 0.8,
            fill: colors.black,
            ease: Power0.easeNone,
            duration: 100
          },
          callback),

          // Column: 0-1
          // Color: gray8B
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 0.3, y: height * 3},
            end: {width: width * 1.05, height: height, x: width * 0.3, y: height * 3}
          },
          _this._sizes,
          {
            isNotRedrawImage: true,
            fill: colors.gray8B,
            ease: Expo.easeOut,
            duration: 200
          },
          callback),

          // Column: 1
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 1.35, y: height * 3},
            end: {width: width * 0.05, height: height, x: width * 1.35, y: height * 3}
          },
          _this._sizes,
          {
            isNotRedrawImage: true,
            fill: colors.redC8,
            ease: Expo.easeOut,
            duration: 200
          },
          callback),

          // END ROW 3
          ], callback)
      },


      /*
       * Row 4
       */
       function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 4

          // Column 5
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: 5.65 * width, y: height * 4},
            end: {width: width * 0.05, height: height, x: 5.65 * width, y: height * 4}
          },
          _this._sizes,
          {ease: Expo.easeIn, isNotRedrawImage: true, fill: colors.orangeE9, duration: 250, delay: firstStep},
          callback),

          // Column 5
          // Color: red75
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 5.8, y: height * 4},
            end: {width: width * 0.2, height: height, x: width * 5.8, y: height * 4}
          },
          _this._sizes,
          {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.red75, duration: 300},
          callback),

          // END ROW 4
          ], callback)
      },


      /*
       * Row 5
       */
       function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 5

          // Column 1
          // Color: black, opacity: 0.4
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width, y: height * 5},
            end: {width: width * 1.4, height: height, x: width, y: height * 5}
          },
          _this._sizes,
          {
            opacity: 0.4, ease: Expo.easeIn, fill: colors.black, duration: 300, delay: firstStep
          },
          callback),

          // Column 2
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 2.4, y: height * 5},
            end: {width: width * 0.05, height: height, x: width * 2.4, y: height * 5}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.pinkFF, duration: 100
          },
          callback),

          // Column 2
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 2.45, y: height * 5},
            end: {width: width * 0.15, height: height, x: width * 2.45, y: height * 5}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.redC8, duration: 200
          },
          callback),

          // END ROW 5
          ], callback);
      },

      /*
       * Row 6
       */
       function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 6

          // Column all
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: 0, y: height * 6, width: 0, height},
              image: {
                x: imageWidth / 5,
                y: imageHeight * 6,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: 0, y: height * 6, width: width * 5.8, height},
              image: {
                x: imageWidth / 5,
                y: imageHeight * 6,
                width: imageWidth * 5.8,
                height: imageHeight
              },
            },
            duration: 1100,
            delay: firstStep,
            ease: Expo.easeInOut
          }),

          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: width * 5.8, y: height * 6 },
            end: { width: width * 0.2, height: height, x: width * 5.8, y: height * 6 },
            duration: 200, ease: Expo.easeOut
          }, callback)

          // END ROW 6
          ], callback);
      },

      /*
       * Row 7
       */
       function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 7

          // Column 0
          // Color: red75
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: 0, y: height * 7},
            end: {width: width * 0.05, height: height, x: 0, y: height * 7}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeIn, fill: colors.red75, duration: 100, delay: firstStep
          },
          callback),
          // Column 0
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 0.05, y: height * 7},
            end: {width: width * 0.95, height: height, x: width * 0.05, y: height * 7}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.redC8, duration: 200
          },
          callback),

          // Column 1-2
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width, y: height * 7, width: 0, height},
              image: {
                x: imageWidth * 0.8,
                y: imageHeight * 7,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width, y: height * 7, width: width * 2, height},
              image: {
                x: imageWidth * 0.8,
                y: imageHeight * 7,
                width: imageWidth * 2,
                height: imageHeight
              },
            },
            duration: 300,
            ease: Power0.easeNone
          }),

          // Column 3
          // Color: red75
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 3, y: height * 7},
            end: {width: width * 0.08, height: height, x: width * 3, y: height * 7}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.red75, duration: 50
          },
          callback),
          // Column 3
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 3.08, y: height * 7},
            end: {width: width * 0.16, height: height, x: width * 3.08, y: height * 7}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.redC8, duration: 200
          },
          callback),

          // Column 3-5
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 3.24, y: height * 7, width: 0, height},
              image: {
                x: imageWidth * 3.02,
                y: imageHeight * 7,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 3.24, y: height * 7, width: width * 3.76, height},
              image: {
                x: imageWidth * 3.02,
                y: imageHeight * 7,
                width: imageWidth * 3.76,
                height: imageHeight
              },
            },
            duration: 800,
            ease: Expo.easeOut,
            onEndRedraw: (context, current) => {
              CanvasRect.draw(context, {
                width: current.canvasWidth, height: current.canvasHeight, x: current.canvasX, y: current.canvasY, fill: colors.black, opacity: 0.4
              });
            }
          }),
          // END ROW 7
          ], callback);
      },

      /*
       * Row 8
       */
       function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 8

          // Column all
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: 0, y: height * 8, width: 0, height},
              image: {
                x: imageWidth / 5,
                y: imageHeight * 8,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: 0, y: height * 8, width: width * 5.8, height},
              image: {
                x: imageWidth / 5,
                y: imageHeight * 8,
                width: imageWidth * 5.8,
                height: imageHeight
              },
            },
            duration: 1100,
            delay: firstStep,
            ease: Expo.easeInOut
          }),

          // Column 5
          // Color: black
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 5.8, y: height * 8},
            end: {width: width * 0.2, height: height, x: width * 5.8, y: height * 8}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.black, duration: 200
          },
          callback),

          // END ROW 8
          ], callback);
      },

      /*
       * Row 9
       */
       function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 9

          // Column 0
          // Color: black
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: 0, y: height * 9},
            end: {width: width * 2.13, height: height, x: 0, y: height * 9}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.black, duration: 200, delay: firstStep
          },
          callback),

          // Column 2
          // Color: red75
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 2.13, y: height * 9},
            end: {width: width * 0.48, height: height, x: width * 2.13, y: height * 9}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.red75, duration: 200
          },
          callback),

          // Column 2
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 2.61, y: height * 9},
            end: {width: width * 0.06, height: height, x: width * 2.61, y: height * 9}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.pinkFF, duration: 200
          },
          callback),

          // Column all
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 2.67, y: height * 9, width: 0, height},
              image: {
                x: imageWidth * 2.87,
                y: imageHeight * 9,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 2.67, y: height * 9, width: width * 3.13, height},
              image: {
                x: imageWidth * 2.87,
                y: imageHeight * 9,
                width: imageWidth * 3.13,
                height: imageHeight
              },
            },
            duration: 600,
            ease: Expo.easeInOut
          }),

          // Column 5
          // Color: black
          (callback) => _this._customRectangleAnimation({
            start: {width: 0, height: height, x: width * 5.8, y: height * 9},
            end: {width: width * 0.2, height: height, x: width * 5.8, y: height * 9}
          },
          _this._sizes,
          {
            isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.black, duration: 200
          },
          callback),

          // END ROW 9
          ], callback);
      },
      ], config);
}

public hide(config?: { onStart?: Function, onEnd?: Function }) {
  super.hide();

  const _this = this;
  const animationDuration = 1000;

  super._handleUnload([
      /*
       * Rows 0
       */
       function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 0, y: 0 },
            end: { width: fullWidth, height: height, x: 0, y: 0 },
            duration: animationDuration, delay: thirdStep
          }, callback)
          ], callback);
      },


      /*
       * Rows 1
       */
       function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height},
            end: {width: fullWidth, height: height, x: 0, y: height},
            duration: animationDuration, delay: firstStep
          }, callback)
          ], callback);
      },


      /*
       * Rows 2
       */
       function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0},
            end: {width: fullWidth, height: height, x: 0},
            duration: animationDuration, delay: secondStep
          }, callback)
          ], callback);
      },


      /*
       * Rows 3
       */
       function(callback) {
        let {fullWidth, height, firstStep} = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 3},
            end: {width: fullWidth, height: height, x: 0, y: height * 3},
            duration: animationDuration, delay: firstStep
          }, callback)
          ], callback);
      },


      /*
       * Row 4
       */
       function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 4},
            end: {width: fullWidth, height: height, x: 0, y: height * 4},
            duration: animationDuration, delay: thirdStep
          }, callback)
          ], callback)
      },


      /*
       * Row 5
       */
       function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 5},
            end: {width: fullWidth, height: height, x: 0, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
          ], callback);
      },


      /*
       * Row 6
       */
       function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 6},
            end: {width: fullWidth, height: height, x: 0, y: height * 6},
            duration: animationDuration, delay: firstStep
          }, callback)
          ], callback);
      },


      /*
       * Row 7
       */
       function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 7},
            end: {width: fullWidth, height: height, x: 0, y: height * 7},
            duration: animationDuration, delay: secondStep
          }, callback)
          ], callback);
      },


      /*
       * Row 8
       */
       function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 8},
            end: {width: fullWidth, height: height, x: 0, y: height * 8},
            duration: animationDuration, delay: firstStep
          }, callback)
          ], callback);
      },


      /*
       * Row 9
       */
       function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 9},
            end: {width: fullWidth, height: height, x: 0, y: height * 9},
            duration: animationDuration, delay: thirdStep
          }, callback)
          ], callback);
      }
      ], config);
}
}



