import {ISlideConfig} from "./ISlideConfig";
import {ISlideController} from "../../shared/slider/ISlideController";

export class Slide {
  private _states = {
    nextState: null, prevState: null, current: null
  };
  private _title: { text: string, name: string }[];
  private _subtitle: { text: string, name: string }[];
  private _comment: { text: string, name: string }[];
  private _image: String;
  private _controller: ISlideController;

  constructor(canvas, config: ISlideConfig, type: string) {
    this._states.nextState = config.nextState;
    this._states.prevState = config.prevState;
    this._states.current = config.state;

    this._title = config.title;
    this._subtitle = config.subtitle;
    this._comment = config.comment;

    this._controller = config.controllerFactory[type].createController(canvas, config);
  }

  get nextState() {
    return this._states.nextState;
  }

  get prevState() {
    return this._states.prevState;
  }

  get state() {
    return this._states.current;
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    this._controller.hide(config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    this._controller.show(config);
  }

  public get title(): { text: string, name: string }[] {
    return this._title;
  }

  public get subtitle(): { text: string, name: string }[] {
    return this._subtitle;
  }

  public get comment(): { text: string, name: string }[] {
    return this._comment;
  }
}



