import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

import { colors } from '../slider-colors';

declare const Power0;
declare const Expo;
declare const async;

export class HomeSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new HomeSlideController(canvas, config);
  }
}

export class HomeSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;

    super._handleLoad([

      /*
       * Row 0
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.parallel([

          // Column 4-7
          (callback) => async.series([
            // Column 3.75 - 6
            // Image animate
            _this._rectangleOffsetAnimate({
              start: {
                canvas: {x: width * 3.75, y: 0, width: 0, height},
                image: {
                  x: imageWidth * 3.5,
                  y: 0,
                  width: 0,
                  height: imageHeight
                },
              },
              end: {
                canvas: {x: width * 3.75, y: 0, width: width * 2.25, height},
                image: {
                  x: imageWidth * 3.5,
                  y: 0,
                  width: imageWidth * 2.25,
                  height: imageHeight
                },
              },
              duration: 700,
              delay: firstStep,
              ease: Expo.easeIn
            }),

            // Column 6
            // Color: orange
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 6 * width, y: 0},
                end: {width: width / 20, height: height, x: 6 * width, y: 0}
              },
              _this._sizes,
              {
                ease: Power0.easeNone,
                isNotRedrawImage: true,
                fill: colors.orangeE9,
                duration: 50
              },
              callback),

            // Column 6
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 6.05 * width, y: 0},
                end: {width: width / 8, height: height, x: 6.05 * width, y: 0}
              },
              _this._sizes,
              {
                ease: Power0.easeNone,
                isNotRedrawImage: true,
                fill: colors.red75,
                duration: 50
              },
              callback),

            // Column 6
            // Image animate
            _this._rectangleOffsetAnimate({
              start: {
                canvas: {x: width * 6.175, y: 0, width: 0, height},
                image: {
                  x: imageWidth * 5.925,
                  y: 0,
                  width: 0,
                  height: imageHeight
                },
              },
              end: {
                canvas: {x: width * 6.175, y: 0, width: width / 16 * 9, height},
                image: {
                  x: imageWidth * 5.925,
                  y: 0,
                  width: imageWidth / 16 * 9,
                  height: imageHeight
                },
              },
              duration: 100,
              ease: Power0.easeNone,
              onEndRedraw: (context, current) => {
                CanvasRect.draw(context, {
                  x: current.canvasX, y: current.canvasY, width: current.canvasWidth, height: current.canvasHeight, fill: colors.black, opacity: 0.5
                });
              }
            }),

            // Column 6
            // Image animate
            _this._rectangleOffsetAnimate({
              start: {
                canvas: {x: width * 6.175 + width / 16 * 9, y: 0, width: 0, height},
                image: {
                  x: imageWidth * 6.5,
                  y: 0,
                  width: 0,
                  height: imageHeight
                },
              },
              end: {
                canvas: {x: width * 6.175 + width / 16 * 9, y: 0, width: width * 0.2, height},
                image: {
                  x: imageWidth * 6.5,
                  y: 0,
                  width: imageWidth * 0.2,
                  height: imageHeight
                },
              },
              duration: 300,
              ease: Expo.easeOut
            }),

          ], callback),

          // Column 7
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 7.75 * width, y: 0},
              end: {width: width / 4, height: height, x: 7.75 * width, y: 0}
            },
            _this._sizes,
            {
              ease: Expo.easeInOut,
              isNotRedrawImage: true,
              fill: colors.redC8,
              duration: 300,
              delay: firstStep + 300
            },
            callback),
        ], callback);
      },


      /*
       * Row 1
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 1

          // Column 3
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 3.2, y: height},
              end: {width: width / 20, height: height, x: width * 3.2, y: height}
            },
            _this._sizes,
            {
              ease: Expo.easeIn,
              isNotRedrawImage: true,
              fill: colors.orangeE9,
              duration: 150,
              delay: firstStep
            },
            callback),

          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 3.25, y: height, width: 0, height},
              image: {
                x: imageWidth * 3.5,
                y: imageHeight,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 3.25, y: height, width: width * 0.75, height},
              image: {
                x: imageWidth * 3.5,
                y: imageHeight,
                width: imageWidth * 0.75,
                height: imageHeight
              },
            },
            duration: 250,
            ease: Power0.easeNone
          }),

          // Column 4
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4, y: height},
              end: {width: width / 10, height: height, x: width * 4, y: height}
            },
            _this._sizes,
            {
              ease: Power0.easeNone,
              isNotRedrawImage: true,
              fill: colors.redC8,
              duration: 100
            },
            callback),

          // Column 4
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4.1, y: height},
              end: {width: width / 20, height: height, x: width * 4.1, y: height}
            },
            _this._sizes,
            {
              ease: Expo.easeNone,
              isNotRedrawImage: true,
              fill: colors.pinkFF,
              duration: 100
            },
            callback),
          // END ROW 1
        ], callback)
      },


      /*
       * Row 2
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 2

          // Column 7
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 6.66 * width, y: height * 2},
              end: {width: width / 20, height: height, x: 6.66 * width, y: height * 2}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.pinkFF, duration: 200, delay: firstStep, ease: Expo.easeIn},
            callback),

          // Column 7
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 6.71 * width, y: height * 2},
              end: {width: width * 0.29, height: height, x: 6.71 * width, y: height * 2}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.red75, duration: 200, ease: Expo.easeOut},
            callback)

          // END ROW 2
        ], callback)
      },


      /*
       * Row 3
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.parallel([
          // ROW 3

          // Column 3-4
          (callback) => async.series([
            // Column 3
            _this._rectangleOffsetAnimate({
              start: {
                canvas: {x: width * 3, y: height * 3, width: 0, height},
                image: {
                  x: imageWidth * 3.25,
                  y: imageHeight * 3,
                  width: 0,
                  height: imageHeight
                },
              },
              end: {
                canvas: {x: width * 3, y: height * 3, width: width, height},
                image: {
                  x: imageWidth * 3.25,
                  y: imageHeight * 3,
                  width: imageWidth,
                  height: imageHeight
                },
              },
              duration: 550,
              ease: Expo.easeIn,
              delay: firstStep
            }),

            // Column 4
            // Color: redC8
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 4, y: height * 3},
                end: {width: width / 10, height: height, x: width * 4, y: height * 3}
              },
              _this._sizes,
              {isNotRedrawImage: true, fill: colors.redC8, duration: 200, ease: Expo.easeOut},
              callback),
            // Column 4
            // Color: black
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 4.1, y: height * 3},
                end: {width: width / 20, height: height, x: width * 4.1, y: height * 3}
              },
              _this._sizes,
              {isNotRedrawImage: true, fill: colors.black, duration: 100, ease: Expo.easeOut},
              callback)
          ], callback),

          // Column 7
          (callback) => async.series([
            // Column 7
            // Color: redC8
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 7.45, y: height * 3},
                end: {width: width / 20, height: height, x: width * 7.45, y: height * 3}
              },
              _this._sizes,
              {isNotRedrawImage: true, fill: colors.redC8, duration: 150, ease: Expo.easeIn, delay: firstStep},
              callback),
            // Column 7
            // Color: orangeE9
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 7.5, y: height * 3},
                end: {width: width / 10, height: height, x: width * 7.5, y: height * 3}
              },
              _this._sizes,
              {isNotRedrawImage: true, fill: colors.orangeE9, duration: 150, ease: Expo.easeOut},
              callback),
          ], callback)

          // END ROW 3
        ], callback)
      },


      /*
       * Row 4
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.parallel([
          // ROW 4

          // Column: 2-3
          (callback) => async.series([
            // Column 2
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 2.75 * width, y: height * 4},
                end: {width: width * 0.2, height: height, x: 2.75 * width, y: height * 4}
              },
              _this._sizes,
              {ease: Expo.easeIn, isNotRedrawImage: true, fill: colors.red75, duration: 250, delay: firstStep},
              callback),
            // Column 2
            // Color: pinkFF
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 2.95 * width, y: height * 4},
                end: {width: width / 20, height: height, x: 2.95 * width, y: height * 4}
              },
              _this._sizes,
              {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.pinkFF, duration: 100},
              callback),
            // Column 3
            // Color: black, opacity: 0.4
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 3 * width, y: height * 4},
                end: {width: width * 1.1, height: height, x: 3 * width, y: height * 4}
              },
              _this._sizes,
              {ease: Expo.easeOut, opacity: 0.4, fill: colors.black, duration: 400},
              callback),
          ], callback),

          // Column: 5
          (callback) => async.series([
            // Column 2
            // Color: black, opacity: 0.4
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 5, y: height * 4},
                end: {width: width * 0.2, height: height, x: width * 5, y: height * 4}
              },
              _this._sizes,
              {ease: Expo.easeIn, fill: colors.black, duration: 250, opacity: 0.4, delay: firstStep},
              callback),
            // Column 2
            // Color: orangeE9
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 5.2, y: height * 4},
                end: {width: width * 0.05, height: height, x: width * 5.2, y: height * 4}
              },
              _this._sizes,
              {ease: Expo.easeOut, fill: colors.orangeE9, duration: 100, isNotRedrawImage: true},
              callback),
          ], callback)

          // END ROW 4
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 5

          // Column 3
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 3.5 * width, y: height * 5},
              end: {width: width / 20, height: height, x: 3.5 * width, y: height * 5}
            },
            _this._sizes,
            {
              isNotRedrawImage: true,ease: Expo.easeIn, fill: colors.redC8, duration: 200, delay: firstStep,
            },
            callback),

          // Column 3-4
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 3.55, y: height * 5, width: 0, height},
              image: {
                x: imageWidth * 3.40,
                y: imageHeight * 5,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 3.55, y: height * 5, width: width * 1.7, height},
              image: {
                x: imageWidth * 3.40,
                y: imageHeight * 5,
                width: imageWidth * 1.7,
                height: imageHeight
              },
            },
            duration: 300,
            ease: Power0.easeNone
          }),

          // Column 5
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 5.25 * width, y: height * 5},
              end: {width: width * 0.3, height: height, x: 5.25 * width, y: height * 5}
            },
            _this._sizes,
            {
              isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.redC8, duration: 100,
            },
            callback),
          // Column 5
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 5.55 * width, y: height * 5},
              end: {width: width / 20, height: height, x: 5.55 * width, y: height * 5}
            },
            _this._sizes,
            {
              isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.red75, duration: 100,
            },
            callback),

          // Column 5
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 5.6, y: height * 5, width: 0, height},
              image: {
                x: imageWidth * 5.45,
                y: imageHeight * 5,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 5.6, y: height * 5, width: width * 0.4, height},
              image: {
                x: imageWidth * 5.45,
                y: imageHeight * 5,
                width: imageWidth * 0.4,
                height: imageHeight
              },
            },
            duration: 50,
            ease: Power0.easeNone
          }),

          // Column 6
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 6 * width, y: height * 5},
              end: {width: width / 20, height: height, x: 6 * width, y: height * 5}
            },
            _this._sizes,
            {
              isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.orangeE9, duration: 50,
            },
            callback),

          // Column 6-7
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 6.05, y: height * 5, width: 0, height},
              image: {
                x: imageWidth * 5.9,
                y: imageHeight * 5,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 6.05, y: height * 5, width: width * 1.5, height},
              image: {
                x: imageWidth * 5.90,
                y: imageHeight * 5,
                width: imageWidth * 1.5,
                height: imageHeight
              },
            },
            duration: 250,
            ease: Power0.easeOut
          }),
          // END ROW 5
        ], callback);
      }
    ], config);
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    super.hide();

    const _this = this;
    const animationDuration = 1000;

    super._handleUnload([
      /*
       * Rows 0
       */
      function(callback) {
        let { fullWidth, width, height, thirdStep } = this;

        async.parallel([
          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 3 * width + width / 4, y: 0 },
            end: { width: fullWidth - 3 * width - width / 4, height: height, x: 3 * width + width / 4, y: 0 },
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 1
       */
      function(callback) {
        let { fullWidth, width, height, firstStep } = this;

        async.parallel([
          // Column 4
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width + 10, y: height},
            end: {width: fullWidth - width * 2 - 10, height: height, x: 2 * width + 10, y: height},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 2
       */
      function(callback) {
        let { fullWidth, width, height, secondStep } = this;

        async.parallel([
          // Column 4
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 3 * width, y: height * 2},
            end: {width: fullWidth - width * 3, height: height, x: 3 * width, y: height * 2},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 3
       */
      function(callback) {
        let {fullWidth, width, height, firstStep} = this;

        async.parallel([
          // Column 2
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width, y: height * 3},
            end: {width: fullWidth - width * 2, height: height, x: 2 * width, y: height * 3},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { fullWidth, width, height, firstStep } = this;

        async.parallel([
          // Column 2
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 2 * width, y: height * 4},
            end: {width: fullWidth - width * 2, height: height, x: 2 * width, y: height * 4},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { fullWidth, width, height, thirdStep } = this;

        async.series([
          // Column 5
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: width * 3, y: height * 5},
            end: {width: fullWidth - width * 3, height: height, x: width * 3, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      }
    ], config);
  }
}



