import {Directive, AfterViewInit, OnDestroy, Renderer} from '@angular/core';

declare const device;

@Directive({
  selector: '[orientationDetector]'
})
export class OrientationDetectorDirective implements AfterViewInit, OnDestroy {

private _dispose: Function;

  constructor(
    private _renderer: Renderer
) {}

  ngAfterViewInit() {
    this._setOrientation();

    if (device.desktop()) {
      this._dispose = this._renderer.listen(window, 'resize', () => this._setOrientation());
    } else {
      this._dispose = this._renderer.listen(window, 'orientationchange', () => this._setOrientation());
    }
  }

  ngOnDestroy(){
    this._dispose();
  }

  private _setOrientation() {
    setTimeout(() => {
      const html = document.documentElement;

      if (window.innerHeight >= window.innerWidth) {
        html.classList.add('_portrait');
        html.classList.remove('_landscape');
      } else {
        html.classList.add('_landscape');
        html.classList.remove('_portrait');
      }
    }, 0);
  }
}




