import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {AppService} from '../../../services/app.service/app.service';

@Component({
  selector: 's-neighborhood',
  templateUrl: './s-neighborhood.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./s-neighborhood.component.styl']
})
export class SNeighborhoodComponent implements OnInit {
  local: any;
  public streetImage;
  public sportImage;
  constructor(
    private _appService: AppService
  ) {
    this.local = {};
  }

  ngOnInit() {
    this.streetImage = this._appService.getImage('streetsImages');
    this.sportImage = this._appService.getImage('sportsImages');
  }

}



