import {ISlideController} from "./ISlideController";
import {ISlideConfig, ISlideSizes} from "../../services/greeting/ISlideConfig";

import * as CanvasImage from '../canvas/image';
import * as CanvasRect from '../canvas/rect';

declare const TweenMax;
declare const Expo;
declare const Power3;
declare const async;
declare const $:any;

export class SlideController implements ISlideController {
  protected _sizes: { canvas: ISlideSizes, image: ISlideSizes };
  protected _blockSizes: { canvas?: ISlideSizes, image?: ISlideSizes } = {};
  protected _canvas;
  protected _config: ISlideConfig;
  protected _image: any;
  private _isShow: boolean = false;
  protected _context;
  protected _paths: { x: number, y: number };
  protected _temp;

  protected $canvas = null;
  protected $wrapper = null;

  protected _duration: { show: number, hide: number } = { show: 1000, hide: 1000 };
  protected _delay: { show: number, hide: number } = { show: 0, hide: 0 };

  constructor(canvas, config: ISlideConfig) {
    TweenMax.defaultEase = Power3.easeInOut;

    this._canvas = canvas;
    this._context = canvas.getContext('2d');
    this._config = config;
    this._sizes = config.sizes;
    this._paths = config.paths || { x: 1, y: 1 };

    this._duration = config.duration || this._duration;
    this._delay = config.delay || this._delay;

    this._blockSizes = {
      image: {
        width: Math.ceil(this._sizes.image.width / this._paths.x),
        height: Math.ceil(this._sizes.image.height / this._paths.y),
      },
      canvas: {
        width: Math.ceil(this._sizes.canvas.width / this._paths.x),
        height: Math.ceil(this._sizes.canvas.height / this._paths.y)
      }
    };

    this.$canvas = config.components && $(config.components.canvas);
    this.$wrapper = config.components && $(config.components.wrapper);
  }

  private _updateCanvas() {
    let canvas = $('<canvas></canvas>')
      .attr('width', this._sizes.canvas.width).attr('height', this._sizes.canvas.height)
      .addClass(this._config.components && this._config.components.canvas.slice(1));

    this._context = canvas[0].getContext('2d');

    this.$wrapper.find('canvas').remove();
    this.$wrapper.append(canvas);

    this._context.clearRect(0, 0, this._sizes.canvas.width, this._sizes.canvas.height);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    this._updateCanvas();

    if (this._isShow) {
      throw new Error('Slide already showing');
    }

    this._isShow = true;
    this._image = new Image();
    this._image.src = this._config.image;
  }

  public hide() {
    this._isShow = false;
  }

  public onShowEnd() {
    this._temp = this._canvas.toDataURL();
    this._isShow = true;
  }

  protected _rectangleOffsetAnimate(_config) {
    let config = Object.assign({}, _config, { image: this._image });

    return (callback) => CanvasImage.animate(this._context, config, callback);
  }

  protected _getImagePixelsPerCanvasPixel(canvas: number, image: number): number {
    return image / canvas;
  }

  protected _getImagePositions(
    canvas: ISlideSizes,
    origin: { canvas: ISlideSizes, image: ISlideSizes }
  ) {
    let xDivider = this._getImagePixelsPerCanvasPixel(origin.canvas.width, origin.image.width);
    let yDivider = this._getImagePixelsPerCanvasPixel(origin.canvas.height, origin.image.height);

    let imageX = xDivider * canvas.x + origin.image.x;
    let imageY = yDivider * canvas.y + origin.image.y;
    let imageWidth = xDivider * canvas.width;
    let imageHeight = xDivider * canvas.height;

    return { imageX, imageY, imageWidth, imageHeight };
  }

  protected _customRectangleAnimation(canvas, origin, inputConfig, callback) {
    let drawCanvas = {
      x: Math.min(canvas.end.x, canvas.start.x),
      y: Math.min(canvas.end.y, canvas.start.y),
      width: Math.max(canvas.end.width, canvas.start.width),
      height: Math.max(canvas.end.height, canvas.start.height),
    };
    let { imageX, imageY, imageWidth, imageHeight } = this._getImagePositions(drawCanvas, origin);
    let imageObject = this._image;

    imageX += inputConfig.offset && inputConfig.offset.x || 0;
    imageY += inputConfig.offset && inputConfig.offset.y || 0;

    let config = {
      start: Object.assign({}, canvas.start),
      end: Object.assign({}, canvas.end),
      opacity: inputConfig.opacity,
      fill: inputConfig.fill,
      duration: inputConfig.duration,
      delay: inputConfig.delay,
      ease: inputConfig.ease,
      onStartRedraw(context, current, config) {
        inputConfig.onStartRedraw && inputConfig.onStartRedraw(context, current, config);

        if (!inputConfig.isNotRedrawImage) {
          CanvasImage.draw(context, {
            imageObject,
            image: { x: imageX, y: imageY, width: imageWidth, height: imageHeight },
            canvas: drawCanvas
          });
        }

        inputConfig.onEndRedraw && inputConfig.onEndRedraw(context, current, config);
      }
    };

    CanvasRect.animate(this._context, config, callback);
  }

  protected get isShow() {
    return this._isShow;
  }

  protected _showSlide(config) {
    TweenMax.fromTo('.s-greeting__canvas-wrapper',
      config.duration,
      { x: config.wrapperX.from },
      { x: config.wrapperX.to });
    TweenMax.fromTo('.s-greeting__canvas-wrapper-after, .s-greeting__canvas-wrapper-before, .s-greeting__canvas',
      config.duration,
      { x: config.innerX.from },
      { x: config.innerX.to });
  }

  protected _handleLoad(callbacks, config: { onStart?: Function, onEnd?: Function } = {}) {
    this._image.onload = () => {
      CanvasImage.draw(this._context, Object.assign({}, this._sizes, { imageObject: this._image }));

      setTimeout(() => {
        const width = window.innerWidth;
        this._showSlide({
          wrapperX: {from: -width, to: 0},
          innerX: {from: width, to: 0},
          duration: this._duration.show
        })
      }, this._delay.show * 1000);

      let firstStep = 900;
      let secondStep = firstStep + 400;
      let thirdStep = secondStep + 350;
      let context = {
        height: this._blockSizes.canvas.height,
        width: this._blockSizes.canvas.width,
        imageWidth: this._blockSizes.image.width,
        imageHeight: this._blockSizes.image.height,
        startImageX: this._sizes.image.x,
        startImageY: this._sizes.image.y,

        firstStep, secondStep, thirdStep
      };

      const wrapperCallbacks = callbacks.map((callback) =>
        callback.bind(context));

      config.onStart && config.onStart();
      async.parallel(wrapperCallbacks, () => {
        config.onEnd && config.onEnd();
        this._temp = this._canvas.toDataURL();
      });
    };
  }

  protected _handleUnload(callbacks, config: { onStart?: Function, onEnd?: Function } = {}) {
    let firstStep = 0;
    let secondStep = firstStep + 200;
    let thirdStep = secondStep + 200;
    let fourthStep = thirdStep + 200;

    let context = {
      height: this._blockSizes.canvas.height,
      width: this._blockSizes.canvas.width,
      imageWidth: this._blockSizes.image.width,
      imageHeight: this._blockSizes.image.height,
      startImageX: this._sizes.image.x,
      startImageY: this._sizes.image.y,
      fullWidth: this._sizes.canvas.width,
      fullHeight: this._sizes.canvas.height,

      firstStep, secondStep, thirdStep, fourthStep
    };

    const wrapperCallbacks = callbacks.map((callback) =>
      callback.bind(context));

    config.onStart && config.onStart();

    async.parallel(wrapperCallbacks, () => {
      config.onEnd && config.onEnd();
    });

    setTimeout(() => {
      const width = window.innerWidth;
      this._showSlide({
        wrapperX: {from: 0, to: width},
        innerX: {from: 0, to: -width},
        duration: this._duration.hide
      })
    }, this._delay.hide * 1000);
  };

  protected _customRectangleClearAnimation(config, callback) {
    CanvasRect.animateClear(this._context, config, callback);
  }
}



