import {Injectable} from '@angular/core';

@Injectable()
export class ModalService {
  local: {
    currentModal?: string;
  };

  constructor() {
    this.local = {
      currentModal: null,
    };
  }

  show(modal) {
    this.local.currentModal = modal;
  }

  hide() {
    this.local.currentModal = null;
  }

  prevent(e) {
    e.stopPropagation();
  }
}



