import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

import { colors } from '../slider-colors';

declare const Power0;
declare const Expo;
declare const async;

export class HomeMobileSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new HomeMobileSlideController(canvas, config);
  }
}

export class HomeMobileSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;

    super._handleLoad([

      /*
       * Row 0
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.parallel([

          // Column 1-4.3
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 1.30, y: height, width: 0, height},
              image: {
                x: imageWidth * 1.30,
                y: imageHeight*1.2,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 1.30, y: height, width: width * 3, height},
              image: {
                x: imageWidth * 1.30,
                y: imageHeight*1.2,
                width: imageWidth * 3,
                height: imageHeight
              },
            },
            duration: 700,
            delay: firstStep,
            ease: Expo.easeIn
          }),

        ], callback);
      },


      /*
       * Row 1
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 1

          // Column 3
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 3.8, y: height*2},
              end: {width: width * 0.05, height: height, x: width * 3.8, y: height*2}
            },
            _this._sizes,
            {
              ease: Expo.easeIn,
              isNotRedrawImage: true,
              fill: colors.orangeE9,
              duration: 150,
              delay: firstStep
            },
            callback),
          // Column 3
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 3.85, y: height*2},
              end: {width: width * 0.15, height: height, x: width * 3.85, y: height*2}
            },
            _this._sizes,
            {
              ease: Power0.easeNone,
              isNotRedrawImage: true,
              fill: colors.red75,
              duration: 100
            },
            callback),

          // Column 4
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4, y: height*2},
              end: {width: width * 1.4, height: height, x: width * 4, y: height*2}
            },
            _this._sizes,
            {
              ease: Expo.easeOut,
              opacity: 0.4,
              fill: colors.black,
              duration: 300
            },
            callback),

          // END ROW 1
        ], callback)
      },


      /*
       * Row 2
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 2

          // Column 0
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: 0, y: height * 3},
              end: {width: width * 0.05, height: height, x: 0, y: height * 3}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.orangeE9, duration: 100, delay: firstStep, ease: Expo.easeIn},
            callback),

          // Column 0
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 0.05, y: height * 2, width: 0, height},
              image: {
                x: imageWidth * 0.30,
                y: imageHeight * 2,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 0.05, y: height * 2, width: width * 0.95, height},
              image: {
                x: imageWidth * 0.30,
                y: imageHeight * 2,
                width: imageWidth * 0.95,
                height: imageHeight
              },
            },
            duration: 300,
            ease: Power0.easeNone
          }),

          // Column 1
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width, y: height * 3},
              end: {width: width * 0.4, height: height, x: width, y: height * 3}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.redC8, duration: 150, ease: Power0.easeNone},
            callback),
          // Column 1
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 1.4, y: height * 3},
              end: {width: width * 0.05, height: height, x: width * 1.4, y: height * 3}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.pinkFF, duration: 100, ease: Power0.easeNone},
            callback),

          // Column 1
          // Image animate
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 1.45, y: height * 3, width: 0, height},
              image: {
                x: imageWidth * 1.70,
                y: imageHeight * 2,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 1.45, y: height * 3, width: width * 0.55, height},
              image: {
                x: imageWidth * 1.70,
                y: imageHeight * 2,
                width: imageWidth * 0.55,
                height: imageHeight
              },
            },
            duration: 300,
            ease: Expo.easeOut
          }),

          // END ROW 2
        ], callback)
      },


      /*
       * Row 3
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 3

          // Column 5
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4.94, y: height * 4},
              end: {width: width * 0.06, height: height, x: width * 4.94, y: height * 4}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.pinkFF, duration: 150, ease: Expo.easeIn, delay: firstStep},
            callback),
          // Column 5
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 5, y: height * 4},
              end: {width: width * 0.4, height: height, x: width * 5, y: height * 4}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.red75, duration: 150, ease: Power0.easeNone},
            callback),
          // Column 5
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 5.4, y: height * 4},
              end: {width: width * 0.6, height: height, x: width * 5.4, y: height * 4}
            },
            _this._sizes,
            {isNotRedrawImage: true, fill: colors.black, duration: 150, ease: Expo.easeOut},
            callback)

          // END ROW 3
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.parallel([
          // ROW 5

          // Column 0-1
          (callback) => async.series([
            // Column 0
            // Color: red75
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: 0, y: height * 6},
                end: {width: width * 0.14, height: height, x: 0, y: height * 6}
              },
              _this._sizes,
              {
                isNotRedrawImage: true, ease: Expo.easeIn, fill: colors.red75, duration: 200, delay: firstStep,
              },
              callback),

            // Column 0
            // Color: pinkFF
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 0.14, y: height * 6},
                end: {width: width * 0.06, height: height, x: width * 0.14, y: height * 6}
              },
              _this._sizes,
              {
                isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.pinkFF, duration: 100
              },
              callback),

            // Column 0-1
            _this._rectangleOffsetAnimate({
              start: {
                canvas: {x: width * 0.2, y: height * 6, width: 0, height},
                image: {
                  x: imageWidth * 0.3,
                  y: imageHeight * 5,
                  width: 0,
                  height: imageHeight
                },
              },
              end: {
                canvas: {x: width * 0.2, y: height * 6, width: width * 1.8, height},
                image: {
                  x: imageWidth * 0.3,
                  y: imageHeight * 5,
                  width: imageWidth * 1.8,
                  height: imageHeight
                },
              },
              duration: 200,
              ease: Power0.easeNone,
              onEndRedraw: (context, current) => {
                CanvasRect.draw(context, {
                  width: current.canvasWidth, height: current.canvasHeight, x: current.canvasX, y: current.canvasY,
                  fill: colors.black, opacity: 0.8
                });
              }
            })
          ], callback),

          // Column 4-5
          (callback) => async.series([
            // Column 4-5
            // Color: black
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 4.3, y: height * 6},
                end: {width: width * 1.05, height: height, x: width * 4.3, y: height * 6}
              },
              _this._sizes,
              {
                opacity: 0.8, ease: Expo.easeIn, fill: colors.black, duration: 200, delay: firstStep,
              },
              callback),

            // Column 5
            // Color: redC8
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 5.35, y: height * 6},
                end: {width: width * 0.06, height: height, x: width * 5.35, y: height * 6}
              },
              _this._sizes,
              {
                isNotRedrawImage: true, ease: Power0.easeNone, fill: colors.redC8, duration: 100
              },
              callback),

            // Column 5
            // Color: orangeE9
            (callback) => _this._customRectangleAnimation({
                start: {width: 0, height: height, x: width * 5.41, y: height * 6},
                end: {width: width * 0.1, height: height, x: width * 5.41, y: height * 6}
              },
              _this._sizes,
              {
                isNotRedrawImage: true, ease: Expo.easeOut, fill: colors.orangeE9, duration: 100
              },
              callback)

          ], callback)

          // END ROW 5
        ], callback);
      },


      /*
       * Row 7
       */
      function(callback) {
        let { width, height, firstStep } = this;

        async.series([
          // ROW 7

          // Column 3
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 2.94, y: height * 8},
              end: {width: width * 0.06, height: height, x: width * 2.94, y: height * 8}
            },
            _this._sizes,
            {
              ease: Expo.easeIn, fill: colors.orangeE9, duration: 200, delay: firstStep, isNotRedrawImage: true
            },
            callback),

          // Column 3-4
          // Color: black, opacity: 0.4
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 3, y: height * 8},
              end: {width: width * 1.9, height: height, x: width * 3, y: height * 8}
            },
            _this._sizes,
            {
              opacity: 0.4, ease: Power0.easeNone, fill: colors.black, duration: 250
            },
            callback),
          // Column 4-5
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4.9, y: height * 8},
              end: {width: width * 0.32, height: height, x: width * 4.9, y: height * 8}
            },
            _this._sizes,
            {
              ease: Expo.easeOut, fill: colors.redC8, duration: 250, isNotRedrawImage: true
            },
            callback),

          // END ROW 7
        ], callback);
      },


      /*
       * Row 8
       */
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 8

          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 0.3, y: height * 9, width: 0, height},
              image: {
                x: 0,
                y: imageHeight * 8,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 0.3, y: height * 9, width: width * 6, height},
              image: {
                x: 0,
                y: imageHeight * 8,
                width: imageWidth * 6,
                height: imageHeight
              },
            },
            duration: 1000,
            delay: firstStep,
            ease: Expo.easeInOut
          })

          // END ROW 8
        ], callback);
      },


      /*
       * Row 9
       
      function(callback) {
        let { width, height, firstStep, imageWidth, imageHeight } = this;

        async.series([
          // ROW 9

          // Column 3
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 0.45, y: height * 9},
              end: {width: width * 0.05, height: height, x: width * 0.45, y: height * 9}
            },
            _this._sizes,
            {
              ease: Expo.easeIn, fill: colors.redC8, duration: 200, delay: firstStep, isNotRedrawImage: true
            },
            callback),

          // Column 0-1 with black overlay
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 0.5, y: height * 9, width: 0, height},
              image: {
                x: imageWidth * 0.2,
                y: imageHeight * 9,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 0.5, y: height * 9, width: width * 1.2, height},
              image: {
                x: imageWidth * 0.2,
                y: imageHeight * 9,
                width: imageWidth * 1.2,
                height: imageHeight
              },
            },
            duration: 300,
            ease: Power0.easeNone,
            onEndRedraw: (context, current) => {
              CanvasRect.draw(context, {
                width: current.canvasWidth, height: current.canvasHeight, x: current.canvasX, y: current.canvasY,
                fill: colors.black, opacity: 0.4
              });
            }
          }),

          // Column 1-3
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 1.7, y: height * 9, width: 0, height},
              image: {
                x: imageWidth * 1.4,
                y: imageHeight * 9,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 1.7, y: height * 9, width: width * 2.3, height},
              image: {
                x: imageWidth * 1.4,
                y: imageHeight * 9,
                width: imageWidth * 2.3,
                height: imageHeight
              },
            },
            duration: 250,
            ease: Power0.easeNone
          }),

          // Column 4
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4, y: height * 9},
              end: {width: width * 0.05, height: height, x: width * 4, y: height * 9}
            },
            _this._sizes,
            {
              ease: Power0.easeNone, fill: colors.pinkFF, duration: 100, isNotRedrawImage: true
            },
            callback),

          // Column 4
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height: height, x: width * 4.05, y: height * 9},
              end: {width: width * 0.15, height: height, x: width * 4.05, y: height * 9}
            },
            _this._sizes,
            {
              ease: Power0.easeNone, fill: colors.orangeE9, duration: 100, isNotRedrawImage: true
            },
            callback),

          // Column 4-5
          _this._rectangleOffsetAnimate({
            start: {
              canvas: {x: width * 4.2, y: height * 9, width: 0, height},
              image: {
                x: imageWidth * 3.9,
                y: imageHeight * 9,
                width: 0,
                height: imageHeight
              },
            },
            end: {
              canvas: {x: width * 4.2, y: height * 9, width: width * 1.8, height},
              image: {
                x: imageWidth * 3.9,
                y: imageHeight * 9,
                width: imageWidth * 1.8,
                height: imageHeight
              },
            },
            duration: 450,
            ease: Expo.easeOut
          })

          // END ROW 9
        ], callback);
      },*/
    ], config);
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    super.hide();

    const _this = this;
    const animationDuration = 1000;

    super._handleUnload([
      /*
       * Rows 0
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 0, y: height },
            end: { width: fullWidth, height: height, x: 0, y: height },
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 1
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height*2},
            end: {width: fullWidth, height: height, x: 0, y: height*2},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 2
       */
      function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0},
            end: {width: fullWidth, height: height, x: 0},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 3
       */
      function(callback) {
        let {fullWidth, height, firstStep} = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 4},
            end: {width: fullWidth, height: height, x: 0, y: height * 4},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 5},
            end: {width: fullWidth, height: height, x: 0, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 6},
            end: {width: fullWidth, height: height, x: 0, y: height * 6},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Row 6
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 7},
            end: {width: fullWidth, height: height, x: 0, y: height * 7},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 7
       */
      function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 8},
            end: {width: fullWidth, height: height, x: 0, y: height * 8},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Row 8
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 9},
            end: {width: fullWidth, height: height, x: 0, y: height * 9},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 9
      
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 10},
            end: {width: fullWidth, height: height, x: 0, y: height * 10},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      } */
    ], config);
  }
}



