import {SlideController} from "../slideController";
import {ISlideControllerFactory, ISlideController} from "../ISlideController";
import {ISlideConfig} from "../../../services/greeting/ISlideConfig";

import * as CanvasRect from '../../canvas/rect';

import {colors} from '../slider-colors';

declare const Power0;
declare const Expo;
declare const async;

export class BuildingMobileSlideControllerFactory implements ISlideControllerFactory {
  public createController(canvas: any, config: ISlideConfig): ISlideController {
    return new BuildingMobileSlideController(canvas, config);
  }
}

export class BuildingMobileSlideController extends SlideController {
  constructor(canvas, config: ISlideConfig) {
    super(canvas, config);
  }

  public show(config?: { onStart?: Function, onEnd?: Function }) {
    super.show();

    const _this = this;
    const delay = 100;

    super._handleLoad([
      /*
       * Row 0
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 0-3
          (callback) => _this._customRectangleClearAnimation({
            start: { x: 0, y: 0, width: 0, height: height },
            end: { x: 0, y: 0, width: width * 3, height: height },
            duration: 900, delay: 0, ease: Expo.easeIn
          }, callback),

          // Column 3
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: width * 3, y: 0 },
              end: { width: width * 0.15, height, x: width * 3, y: 0 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.orangeE9, duration: 20 },
            callback),
          // Column 3
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: width * 3.15, y: 0 },
              end: { width: width * 0.15, height, x: width * 3.15, y: 0 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.redC8, duration: 40 },
            callback),
          // Column 3
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: width * 3.3, y: 0 },
              end: { width: width * 0.7, height, x: width * 3.3, y: 0 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.black, duration: 60 },
            callback),
          // Column 4
          // Color: black, opacity 0.5
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: width * 4, y: 0 },
              end: { width: width / 2, height, x: width * 4, y: 0 }
            },
            _this._sizes,
            { ease: Expo.easeOut, fill: colors.black, opacity: 0.5, duration: 50 },
            callback)
          ], callback);
      },


      /*
       * Row 1
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 1
          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height, width: 0, height: height},
            end: {x: 0, y: height, width: width * 1.5, height: height},
            duration: 1100, delay: delay, ease: Expo.easeIn
          }, callback),
          // Column 1
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.5, y: height},
              end: {width: width * 0.4, height, x: width * 1.5, y: height}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.redC8, duration: 100},
            callback),
          // Column 1
          // Color: black
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.9, y: height},
              end: {width: width * 0.1, height, x: width * 1.9, y: height}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: colors.black, duration: 100},
            callback)
          // END ROW 1
        ], callback);
      },


      /*
       * Row 2
       */
      function (callback) {
        let { width, height } = this;

        async.series([
          // ROW 2
          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height * 2, width: 0, height: height},
            end: {x: 0, y: height * 2, width: width * 1.2, height: height},
            duration: 1100, delay: delay * 2, ease: Expo.easeIn
          }, callback),
          // Column 1
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.2, y: height * 2},
              end: {width: width * 0.1, height, x: width * 1.2, y: height * 2}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.pinkFF, duration: 50},
            callback),
          // Column 1
          // Clear
          (callback) => _this._customRectangleClearAnimation({
            start: {x: width * 1.3, y: height * 2, width: 0, height: height},
            end: {x: width * 1.3, y: height * 2, width: width * 0.55, height: height},
            duration: 100, delay: 0, ease: Power0.easeNone
          }, callback),
          // Column 1
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.85, y: height * 2},
              end: {width: width * 0.15, height, x: width * 1.85, y: height * 2}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.red75, duration: 100},
            callback),
          // Column 4
          // Color: black, opacity 0.5
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 2, y: height * 2},
              end: {width: width / 3, height, x: width * 2, y: height * 2}
            },
            _this._sizes,
            {ease: Expo.easeOut, fill: colors.black, opacity: 0.5, duration: 150},
            callback)

          // END ROW 2
        ], callback);
      },


      /*
       * Row 3
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 3

          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height * 3, width: 0, height: height},
            end: {x: 0, y: height * 3, width: width * 1.85, height: height},
            duration: 1100, delay: delay * 3, ease: Expo.easeIn
          }, callback),

          // Column 1
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.85, y: height * 3},
              end: {width: width * 0.15, height, x: width * 1.85, y: height * 3}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.red75, duration: 100},
            callback),

          // END ROW 3
        ], callback);
      },


      /*
       * Row 5
       */
      function(callback) {
        let { width, height, secondStep } = this;

        async.series([
          // Column 5
          // Color: pinkFF
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 5, y: height * 5},
              end: {width: width * 0.05, height, x: width * 5, y: height * 5}
            },
            _this._sizes,
            {ease: Expo.easeIn, isNotRedrawImage: true, fill: colors.pinkFF, duration: 100, delay: secondStep},
            callback),
          // Column 5
          // Color: black, opacity: 0.8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 5.05, y: height * 5},
              end: {width: width * 0.95, height, x: width * 5.05, y: height * 5}
            },
            _this._sizes,
            {ease: Expo.easeOut, opacity: 0.8, fill: colors.black, duration: 250},
            callback),
        ], callback);
      },


      /*
       * Row 6
       */
      function(callback) {
        let { width, height, secondStep } = this;

        async.series([
          // Column 0
          // Color: black, opacity: 0.8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: 0, y: height * 6},
              end: {width: width * 0.95, height, x: 0, y: height * 6}
            },
            _this._sizes,
            {ease: Expo.easeIn, opacity: 0.8, fill: colors.black, duration: 300, delay: secondStep + delay * 2},
            callback),
          // Column 0
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 0.95, y: height * 6},
              end: {width: width * 0.05, height, x: width * 0.95, y: height * 6}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: colors.redC8, duration: 100},
            callback),
          // Column 1
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width, y: height * 6},
              end: {width: width * 0.1, height, x: width, y: height * 6}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: colors.orangeE9, duration: 150},
            callback)
        ], callback);
      },


      /*
       * Row 7
       */
      function(callback) {
        let { width, height, secondStep } = this;

        async.series([
          // Column 1
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.6, y: height * 7},
              end: {width: width * 0.06, height, x: width * 1.6, y: height * 7}
            },
            _this._sizes,
            {ease: Expo.easeIn, isNotRedrawImage: true, fill: colors.red75, duration: 100, delay: secondStep + delay * 4},
            callback),
          // Column 1
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.66, y: height * 7},
              end: {width: width * 0.35, height, x: width * 1.66, y: height * 7}
            },
            _this._sizes,
            {ease: Expo.easeOut, isNotRedrawImage: true, fill: colors.black, duration: 300},
            callback),
        ], callback);
      },


      /*
       * Row 8
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 8

          (callback) => _this._customRectangleClearAnimation({
            start: {x: 0, y: height * 8, width: 0, height: height},
            end: {x: 0, y: height * 8, width: width, height: height},
            duration: 600, delay: delay * 7, ease: Expo.easeIn,
            onEndRedraw: (context, current) => {
              CanvasRect.draw(context, {
                x: current.x, y: current.y, width: current.width, height: current.height, fill: colors.black, opacity: 0.4
              });
            }
          }, callback),

          // Column 1
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width, y: height * 8},
              end: {width: width * 0.15, height, x: width, y: height * 8}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.redC8, duration: 100},
            callback),

          // Column 1
          // Color: red75
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.15, y: height * 8},
              end: {width: width * 0.25, height, x: width * 1.15, y: height * 8}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.red75, duration: 100},
            callback),

          // Column 1
          // Color: orangeE9
          (callback) => _this._customRectangleAnimation({
              start: {width: 0, height, x: width * 1.40, y: height * 8},
              end: {width: width * 0.05, height, x: width * 1.40, y: height * 8}
            },
            _this._sizes,
            {ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.orangeE9, duration: 100},
            callback),

          // Column 1
          (callback) => _this._customRectangleClearAnimation({
            start: {x: width * 1.45, y: height * 8, width: 0, height: height},
            end: {x: width * 1.45, y: height * 8, width: width * 0.55, height: height},
            duration: 400, ease: Expo.easeOut,
          }, callback)

        ], callback);
      },

      /*
       * Row 9
       */
      function(callback) {
        let { width, height } = this;

        async.series([
          // ROW 0-3
          (callback) => _this._customRectangleClearAnimation({
            start: { x: 0, y:  height * 9, width: 0, height: height },
            end: { x: 0, y: height * 9, width: width * 2.9, height: height },
            duration: 900, ease: Expo.easeIn, delay: delay * 9
          }, callback),

          // Column 3
          // Color: redC8
          (callback) => _this._customRectangleAnimation({
              start: { width: 0, height, x: width * 2.9, y: height * 9 },
              end: { width: width * 0.1, height, x: width * 2.9, y: height * 9 }
            },
            _this._sizes,
            { ease: Power0.easeNone, isNotRedrawImage: true, fill: colors.redC8, duration: 40 },
            callback),
        ], callback);
      },

    ], config);
  }

  public hide(config?: { onStart?: Function, onEnd?: Function }) {
    super.hide();

    const _this = this;
    const animationDuration = 1000;

    super._handleUnload([
      /*
       * Rows 0
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: { width: 0, height: height, x: 0, y: 0 },
            end: { width: fullWidth, height: height, x: 0, y: 0 },
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 1
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height},
            end: {width: fullWidth, height: height, x: 0, y: height},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 2
       */
      function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0},
            end: {width: fullWidth, height: height, x: 0},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Rows 3
       */
      function(callback) {
        let {fullWidth, height, firstStep} = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 3},
            end: {width: fullWidth, height: height, x: 0, y: height * 3},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 4
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.parallel([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 4},
            end: {width: fullWidth, height: height, x: 0, y: height * 4},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback)
      },


      /*
       * Row 5
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 5},
            end: {width: fullWidth, height: height, x: 0, y: height * 5},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      },


      /*
       * Row 6
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 6},
            end: {width: fullWidth, height: height, x: 0, y: height * 6},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 7
       */
      function(callback) {
        let { fullWidth, height, secondStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 7},
            end: {width: fullWidth, height: height, x: 0, y: height * 7},
            duration: animationDuration, delay: secondStep
          }, callback)
        ], callback);
      },


      /*
       * Row 8
       */
      function(callback) {
        let { fullWidth, height, firstStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 8},
            end: {width: fullWidth, height: height, x: 0, y: height * 8},
            duration: animationDuration, delay: firstStep
          }, callback)
        ], callback);
      },


      /*
       * Row 9
       */
      function(callback) {
        let { fullWidth, height, thirdStep } = this;

        async.series([
          (callback) => _this._customRectangleClearAnimation({
            start: {width: 0, height: height, x: 0, y: height * 9},
            end: {width: fullWidth, height: height, x: 0, y: height * 9},
            duration: animationDuration, delay: thirdStep
          }, callback)
        ], callback);
      }
    ], config);
  }
}



