import {Component, OnInit, Output, EventEmitter, Input, ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'm-greeting',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './m-greeting.component.html',
  styleUrls: ['./m-greeting.component.styl']
})
export class MGretingComponent implements OnInit {

  @Output() onChange: EventEmitter<string> = new EventEmitter();
  @Input() isOpen: boolean = false;
  @Output() onOpenMenu: EventEmitter<boolean> = new EventEmitter();

  public menu = [
    { state: 'building', title: 'Building' },
    { state: 'neighborhood', title: 'Neighborhood' },
    { state: 'contact', title: 'Contact' }
  ];

  constructor() {
  }

  ngOnInit() {
  }

  public goToState(state: string) {
    this.onChange.emit(state);
    this.onOpenMenu.emit(!this.isOpen);
  }

  onClick(state: boolean) {
    this.onOpenMenu.emit(state);
  }
}



