import {Component, OnInit, ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'f-contact',
  templateUrl: './f-contact.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./f-contact.component.styl']
})
export class FContactComponent implements OnInit {
  name: any;
  email: any;
  phone: string;
  submitButtonWasClicked: boolean = false;
  emailValid: boolean = false;
  nameValid: boolean = false;
  formValid: boolean = false;

  constructor() {
  }

  ngOnInit() {
  }

  submitForm() {
    this.submitButtonWasClicked = true;

    this.checkEmail();
    this.checkName();

    this.formValid = this.nameValid && this.emailValid;
  }

  checkName() {
    this.nameValid = this.name && this.name.length !== 0;
  }

  checkEmail() {
    this.emailValid = /^[-a-z0-9~!$%^&*_=}{\'?]+(\.[-a-z0-9~!$%^&*_=+}{\'?]+)*@([a-z0-9_][-a-z0-9_]*(\.[-a-z0-9_]+)*\.(aero|arpa|biz|com|coop|edu|gov|info|int|mil|museum|name|net|org|pro|cat|travel|mobi|[a-z][a-z])|([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}))(:[0-9]{1,5})?$/i.test(this.email);
  }

}



