import {Component, ElementRef, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, NavigationEnd, NavigationStart, Router} from "@angular/router";
import {GreetingService} from './services/greeting/greeting.service';

@Component({
  selector: 'app',
  templateUrl: './app.component.html',
  styleUrls: ['app.component.styl', 'app.styl'],
  encapsulation: ViewEncapsulation.None,
  providers: [GreetingService]
})
export class AppComponent {
  local: {
    previousRouteClassName: null | string
  };
  public isHiddenFooter: boolean = true;

  constructor(public route: ActivatedRoute,
              private router: Router,
              public _greetingService: GreetingService,
              private element: ElementRef) {
    this.local = {
      previousRouteClassName: null
    };

    router.events.subscribe((val) => {
      let currentRoute = this.route.root;
      while (currentRoute.children[0] !== undefined) {
        currentRoute = currentRoute.children[0];
      }
      if (val instanceof NavigationStart) {
        this.local.previousRouteClassName = currentRoute.snapshot.data['className'];
      } else if (val instanceof NavigationEnd) {
        if (this.element) {
          this.element.nativeElement['classList'].remove(this.local.previousRouteClassName);
        }
        if (this.element) {
          this.element.nativeElement['classList'].add(currentRoute.snapshot.data['className']);
        }
      }
    });
  }

  get isFooterVisible() {
    return this._greetingService.isFooterVisible;
  }
}



