import {Component, OnInit, ViewEncapsulation, Input, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'b-greeting-control',
  templateUrl: './b-greeting-control.component.html',
  styleUrls: ['./b-greeting-control.component.styl'],
  encapsulation: ViewEncapsulation.None
})
export class BGreetingControlComponent implements OnInit {

  @Output() onState: EventEmitter<string> = new EventEmitter();
  @Input() prev: any;
  @Input() next: any;

  @Input()
  set isDisable(value: boolean) {
    if (value) {
      this._isButtonDisable = true;
    } else {
      setTimeout(() => {
        this._isButtonDisable = false;
      }, 1500);
    }
  }

  public _isButtonDisable: boolean = true;

  constructor() { }

  ngOnInit() {
  }

  public changeState(state) {
    this.onState.emit(state);
  }
}



