import {Component, OnInit, ViewEncapsulation, Input} from '@angular/core';

@Component({
  selector: 'b-greeting-title',
  templateUrl: './b-greeting-title.component.html',
  styleUrls: ['./b-greeting-title.component.styl'],
  encapsulation: ViewEncapsulation.None
})
export class BGreetingTitleComponent implements OnInit {

  @Input() isFastShowing: boolean = false;
  @Input() title: string[];
  @Input() subtitle: string[];
  @Input() comment: string[];
  @Input() isOutRight: boolean;
  @Input()
  set isCenter(value: boolean) {
    if (value && !this.isFastShowing) {
      setTimeout(() => {
        this._isCenter = value;
      }, 1500);
    } else {
      this._isCenter = value;
    }
  }

  public _isCenter: boolean;

  constructor() { }

  ngOnInit() {
  }

}



