import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {AppService} from '../../../services/app.service/app.service';

@Component({
  selector: 'l-options',
  templateUrl: './l-options.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./l-options.component.styl']
})
export class LOptionsComponent implements OnInit {
  public config: any;
  public images: any;

  @Input() slick: any;
  constructor(
    public _appService: AppService
  ) { }

  ngOnInit() {
    this.images = [...this._appService.getImage('prevOptionsImages')];
  }

  clicked(event, n) {
    event.preventDefault();
    console.log(3);
    console.log(n);
    this.slick.slick('slickGoTo' , n, true);
    this.slick.parent().addClass('_active');
  }

}



