import {Routes} from '@angular/router';
import {HomeComponent} from "./containers/home/home.component";
import {ContactComponent} from "./containers/contact/contact.component";
import {NeighborhoodComponent} from "./containers/neighborhood/neighborhood.component";
import {BuildingComponent} from "./containers/building/building.component";

export const ROUTES: Routes = [
  {
    path: '',
    component: HomeComponent,
    data: {
      className: 'p-home'
    }
  },
  {
    path: 'contact',
    component: ContactComponent,
    data: {
      className: 'p-contact'
    }
  },
  {
    path: 'neighborhood',
    component: NeighborhoodComponent,
    data: {
      className: 'p-neighborhood'
    }
  },
  {
    path: 'building',
    component: BuildingComponent,
    data: {
      className: 'p-building'
    }
  }
];



