import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {GreetingService} from "../../../services/greeting/greeting.service";

@Component({
  selector: 'b-page-title',
  templateUrl: './b-page-title.component.html',
  styleUrls: ['./b-page-title.component.styl'],
  encapsulation: ViewEncapsulation.None
})
export class BPageTitleComponent implements OnInit {

  public isShow: boolean = false;
  public isOutRight: boolean = false;

  constructor(
    private _greetingService: GreetingService
  ) { }

  ngOnInit() {
  }

  public get current(): any {
    return this._greetingService.current || {};
  }

  public get title() {
    return this.current.title || [];
  }
  public get subtitle() {
    return this.current.subtitle || [];
  }
  public get comment() {
    return this.current.comment || [];
  }

  public setVisibleTitle() {
    this.isShow = true;
  }
}



