import {AfterViewInit, Directive, ElementRef, EventEmitter, Input, Output} from '@angular/core';

declare const $: any;

@Directive({
  selector: '[appSlick]'
})
export class SlickDirective implements AfterViewInit {
  statusField: any;
  $slick: any;
  @Input() isCounterInit: boolean;
  @Input() config;
  @Output() getSlickElement: EventEmitter<any> = new EventEmitter();
  constructor(
    public _el: ElementRef,
  ) {
  }



  ngAfterViewInit() {
    this.$slick = $(this._el.nativeElement);
    this.statusField = this.$slick.parent().find('.sl-samples__pages');

    if (this.isCounterInit) {
      this.counter(this.$slick);
    }
    this.$slick.slick(this.config);

    this.getSlickElement.emit(this.$slick);

    // this.$slick.slick('setPosition');
    // setInterval(() => {
    //   console.log('111');
    //   console.log(this.$slick);
    //   this.$slick.slick('setPosition');
    // }, 1000);
  }

  counter($slick) {
    const _this = this;

    $slick
      .on('init reInit afterChange', function (event, slick, currentSlide, nextSlide) {
        const index = (currentSlide ? currentSlide : 0) + 1;
        _this.statusField.text(index + '/' + slick.slideCount);
      });
  }
}



