import {
  Directive, Input, ElementRef, Renderer, Output, EventEmitter, AfterViewInit,
  OnDestroy
} from '@angular/core';

@Directive({
  selector: '[animateTitle]'
})
export class AnimateTitleDirective implements AfterViewInit, OnDestroy {
  @Input() set offset(value: number) {
    this._offset = value;
  }
  @Output() onScrollToElement: EventEmitter<any> = new EventEmitter();

  private _elementOffsetTop = null;
  private _element;
  private _offset: number = 0;
  private _dispose: Function = () => {};

  constructor(
    _el: ElementRef,
    private _renderer: Renderer
  ) {
    this._element = _el.nativeElement;
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.calculatePosition(this._element);

      this._dispose = this._renderer.listen(window, 'scroll', this._onScroll.bind(this));
      this._onScroll();
    }, 100);
  }

  ngOnDestroy() {
    this._dispose();
  }

  private _onScroll() {
    if (window.pageYOffset + window.innerHeight - this._offset > this._elementOffsetTop) {
      this.onScrollToElement.emit();
      this._dispose();
    }
  }

  private calculatePosition(elem) {
    const coords = elem.getBoundingClientRect();
    const height = Math.max(document.documentElement.scrollHeight, window.innerHeight || 0);

    this._elementOffsetTop = coords.top + window.pageYOffset;

    if (window.innerHeight < 800) {
      this._offset = this._offset / 3;
    }

    if (height - 30 < coords.bottom + window.pageYOffset + this._offset) {
      this._elementOffsetTop = height - 30 - this._offset;
    }
  }
}



