import {AfterViewInit, Directive, ElementRef, HostListener, Input, Renderer} from '@angular/core';

declare const $: any;

@Directive({
  selector: '[animate]'
})
export class AnimateDirective implements AfterViewInit {
  private _defaultOffset = 0;
  private _defaultClassName = '';

  @Input('offset') offset: number;
  @Input('className') className: string;

  local: {
    elem?: any;
    pos?: number;
    className?: string;
    elemOffsetTop?: number;
  };

  constructor(public _el: ElementRef, private _render: Renderer) {
  }

  ngAfterViewInit() {
    this.local = {
      elem: this._el.nativeElement,
      pos: this.offset || this._defaultOffset,
      className: this.className || this._defaultClassName,
      elemOffsetTop: null
    };

    setTimeout(() => {
      this.calculatePosition(this.local.elem);
      this.onWindowScroll();
    }, 100);
  }

  @HostListener('window:scroll', ['$event'])

  onWindowScroll() {
    if (!this.local.elemOffsetTop) {
      return;
    }

    if (window.pageYOffset + window.innerHeight - this.local.pos > this.local.elemOffsetTop) {
      this.addClassName(this.local.className);
    }
  }

  private calculatePosition(elem) {
    const coords = elem.getBoundingClientRect();
    const height = Math.max(document.documentElement.scrollHeight, window.innerHeight || 0);

    this.local.elemOffsetTop = coords.top + window.pageYOffset;

    if (window.innerHeight < 800) {
      this.local.pos = this.local.pos / 3;
    }

    if (height - 30 < coords.bottom + window.pageYOffset + this.local.pos) {
      this.local.elemOffsetTop = height - 30 - this.local.pos;
    }
  }

  private addClassName(className: string) {
    this._render.setElementClass(this.local.elem, className, true);
  }
}




